import React, { useCallback } from "react";
import { useSelector } from "react-redux";
import { List, Typography, Grid } from "@material-ui/core";
import CustomFieldItem from "./CustomFieldItem";
import TimeStampEditor from "./TimeStampEditor";
import TimeTracking from "./TimeTracking";
import LastModificationView from "./LastModificationView";

const CustomFieldsList = () => {
  const { trad, configuration, userProfile} = useSelector((state) => ({
    trad: state.translations.values,
    configuration: state.configuration.values,
    userProfile: state.quickSearch.userProfile,
  }));

  const { field_configs, certificate_configs } = userProfile;
  
  //mthode qui determine si les timestamp existent pour afficher ou non le timeStamp editor
  const newCustomFields = {};
  Object.keys(userProfile.enrollments).map((element) => {
    return (newCustomFields[element] = userProfile.custom_field[element]);
  });
  const isTimeStampExist = Object.values(newCustomFields)

  let displayTimeStamp = false
  isTimeStampExist.forEach((element) => {
    if (element) {
      displayTimeStamp = true
    }
  })

  // check if there is courses with time tracking
  const timeTrackingArray = Object.values(userProfile.enrollments).filter(course => (
    course.time_tracking !== undefined
))

  // check if is it allowed to display time tracking
  let displayTimeTracking = false
  if (configuration){
    if (configuration.DISPLAY_TIME_TRACKING) {
      displayTimeTracking = true
    }
  }

  const renderConfigList = useCallback((fieldsList, type, title) => {
    if (fieldsList.length) {
      return (
        <Grid style={{ marginBottom: "35px" }}>
          <Grid
            container
            justify="center"
            direction="column"
            alignItems="center"
            style={{ marginBottom: "15px" }}
          >
            <Grid item>
              <Typography component="h6" variant="h6">
                {title}
              </Typography>
            </Grid>
          </Grid>
          {fieldsList.map((field, index) => {
            return <CustomFieldItem key={index} field={field} config={type} />;
          })}
        </Grid>
      );
    }
    return null;
  }, []);

  const renderTimeStampEditor = useCallback(() => {
    return (
      <Grid style={{ marginBottom: "35px" }}>
        <Grid
          container
          justify="center"
          direction="column"
          alignItems="center"
          style={{ marginBottom: "15px" }}
        >
          <Grid item>
            <Typography component="h6" variant="h6">
              {trad.time_stamp_editor}
            </Typography>
          </Grid>
        </Grid>
        <TimeStampEditor />
      </Grid>
    );
  }, [trad]);

  const renderLastModificationView = useCallback(() => {
    return (
      <Grid style={{ marginBottom: "35px" }}>
        <Grid
          container
          justify="center"
          direction="column"
          alignItems="center"
          style={{ marginBottom: "15px" }}
        >
          <Grid item>
            <Typography component="h6" variant="h6">
            {trad.last_modifications}
            </Typography>
          </Grid>
        </Grid>
        <LastModificationView />
      </Grid>
    );
  }, [trad]);

  const renderTimeTrackingView = useCallback(() => {
    return (
      <Grid style={{ marginBottom: "35px" }}>
        <Grid
          container
          justify="center"
          direction="column"
          alignItems="center"
          style={{ marginBottom: "15px" }}
        >
          <Grid item>
            <Typography component="h6" variant="h6">
            {trad.time_tracking}
            </Typography>
          </Grid>
        </Grid>
        <TimeTracking />
      </Grid>
    );
  }, [trad]);


  return (
    <>
      <List component="nav" aria-labelledby="nested-list-subheader">
        {renderConfigList(
          field_configs,
          "custom_field",
          trad.custom_field_editor
        )}
        {displayTimeStamp &&
          renderTimeStampEditor()
        }
        {renderConfigList(
          certificate_configs,
          "certificate_form_extra",
          trad.certificate_form_extra_editor
        )}
        {displayTimeTracking && timeTrackingArray.length > 0 && renderTimeTrackingView()}
        {userProfile.custom_field.last_update_date && renderLastModificationView()}
      </List>
    </>
  );
};

export default CustomFieldsList;
