const descendingComparator = (a, b, orderBy) => {
  const regularComparators = ["is_active", "last_name", "username"];
  if (regularComparators.indexOf(orderBy) !== -1) {
    if (b[orderBy] < a[orderBy]) {
      return -1;
    }
    if (b[orderBy] > a[orderBy]) {
      return 1;
    }
    return 0;
  } else {
    // courses enrollments status are sorted by enrollment date
    const aCopy = { ...a };
    const bCopy = { ...b };
    if (!aCopy["enrollments"].hasOwnProperty(orderBy)) {
      aCopy["enrollments"][orderBy] = { is_active: false };
    }
    if (!bCopy["enrollments"].hasOwnProperty(orderBy)) {
      bCopy["enrollments"][orderBy] = { is_active: false };
    }
    if (
      !bCopy["enrollments"][orderBy]["is_active"] &&
      aCopy["enrollments"][orderBy]["is_active"]
    ) {
      return -1;
    }
    if (
      bCopy["enrollments"][orderBy]["is_active"] &&
      !aCopy["enrollments"][orderBy]["is_active"]
    ) {
      return 1;
    }
  }
};

const getComparator = (order, orderBy) => {
  return order === "desc"
    ? (a, b) => descendingComparator(a, b, orderBy)
    : (a, b) => -descendingComparator(a, b, orderBy);
};

const stableSort = (array, comparator) => {
  const stabilizedThis = array.map((el, index) => [el, index]);
  stabilizedThis.sort((a, b) => {
    const order = comparator(a[0], b[0]);
    if (order !== 0) return order;
    return a[1] - b[1];
  });
  return stabilizedThis.map((el) => el[0]);
};

const EnhancedTableHead = ({
  classes,
  onSelectAllClick,
  order,
  orderBy,
  numSelected,
  rowCount,
  onRequestSort,
  courses,
  selectedCourseId,
}) => {
  const [headCells, setHeadCells] = useState([]);

  const { trad } = useSelector((state) => ({
    trad: state.translations.values,
  }));

  useEffect(() => {
    const cells = [
      {
        id: "is_active",
        numeric: false,
        disablePadding: true,
        label: trad.user_status,
      },
      {
        id: "last_name",
        numeric: false,
        disablePadding: false,
        label: trad.name,
      },
      {
        id: "username",
        numeric: false,
        disablePadding: false,
        label: trad.username,
      },
    ];

    if (courses[selectedCourseId]) {
      const courseViewCells = [...cells];
      courseViewCells.push({
        id: selectedCourseId,
        numeric: false,
        disablePadding: false,
        label: trad.enrollment_status,
      });

      if (Object.values(courses[selectedCourseId].cohorts).length) {
        courseViewCells.push({
          id: "cohort",
          // id: cohort,
          numeric: false,
          disablePadding: false,
          label: trad.cohort,
        });
      }

      setHeadCells(courseViewCells);
    }
  }, [courses, selectedCourseId, trad]);

  const createSortHandler = (property) => (event) => {
    onRequestSort(event, property);
  };

  return (
    <TableHead>
      <TableRow>
        <TableCell padding="checkbox">
          <Checkbox
            indeterminate={numSelected > 0 && numSelected < rowCount}
            checked={rowCount > 0 && numSelected === rowCount}
            onChange={onSelectAllClick}
            inputProps={{ "aria-label": "select user" }}
          />
        </TableCell>
        {headCells.map((headCell, index) => (
          <TableCell
            key={headCell.id}
            align={headCell.numeric ? "right" : "left"}
            padding={headCell.disablePadding ? "none" : "default"}
            sortDirection={orderBy === headCell.id ? order : false}
          >
            <TableSortLabel
              active={orderBy === headCell.id}
              direction={orderBy === headCell.id ? order : "asc"}
              onClick={createSortHandler(headCell.id)}
            >
              <Box component="div" className={classes.tableHeadCell}>
                <Box component="div">{headCell.label}</Box>
              </Box>

              {orderBy === headCell.id ? (
                <Box component="span" className={classes.visuallyHidden}>
                  {order === "desc" ? "sorted descending" : "sorted ascending"}
                </Box>
              ) : null}
            </TableSortLabel>
          </TableCell>
        ))}
      </TableRow>
    </TableHead>
  );
};

