const GlobalEnrollmentsView = () => {
  return <Grid>Global</Grid>;
};

const DataTable = () => {
  const classes = useStyles();
  const [order, setOrder] = useState("asc");
  const [orderBy, setOrderBy] = useState("username");
  const [selected, setSelected] = useState([]);
  const [rowsPerPage, setRowsPerPage] = useState(10);

  // Redux store setup
  const {
    enrollments,
    selectedEnrollments,
    globalMode,
    courses,
    isLoading,
    selectedCourse,
    selectedCohorts,
    cohort,
    page,
    searchBarResult,
    searchValue,
    showSuggestions,
    loadedCourses,
  } = useSelector((state) => ({
    enrollments: state.userManagement.enrollments,
    selectedEnrollments: state.userManagement.selectedEnrollments,
    globalMode: state.userManagement.displayGlobalMode,
    courses: state.userManagement.courses,
    isLoading: state.userManagement.isLoading,
    selectedCourse: state.userManagement.selectedCourse,
    selectedCohorts: state.userManagement.selectedCohorts,
    cohort: state.userManagement.cohort,
    page: state.userManagement.page,
    searchBarResult: state.userManagement.searchBarResult,
    searchValue: state.userManagement.searchValue,
    showSuggestions: state.userManagement.showSuggestions,
    trad: state.translations.values,
    loadedCourses: state.userManagement.loadedCourses,
  }));

  const [data, setData] = useState(enrollments);

  const dispatch = useDispatch();

  const fetchAllCourseEnrollments = useCallback(
    (selectedCourse) => {
      dispatch(setLoader(true));
      dispatch(fetchCourseEnrollments(selectedCourse));
    },
    [dispatch]
  );

  useEffect(() => {
    if (!globalMode) {
      if (!loadedCourses.includes(selectedCourse)) {
        fetchAllCourseEnrollments(selectedCourse);
      }
    }
  }, [selectedCourse, fetchAllCourseEnrollments, globalMode, loadedCourses]);

  useEffect(() => {
    if (!isLoading && !globalMode) {
      if (Object.keys(searchBarResult).length && searchValue.length > 2) {
        setData(searchBarResult);
      } else if (Object.keys(cohort).length) {
        setData(selectedCohorts);
      } else {
        setData(selectedEnrollments);
      }
    }
  }, [
    selectedEnrollments,
    searchBarResult,
    searchValue,
    enrollments,
    globalMode,
    cohort,
    selectedCohorts,
    isLoading,
  ]);

  // Data sorting
  const handleRequestSort = useCallback(
    (event, property) => {
      const isAsc = orderBy === property && order === "asc";
      setOrder(isAsc ? "desc" : "asc");
      setOrderBy(property);
    },
    [orderBy, order]
  );

  // Data selection
  const handleSelectAllClick = useCallback(
    (event) => {
      if (event.target.checked) {
        const newSelecteds = Object.values(data).map((userData) => userData.id);
        setSelected(newSelecteds);
        return;
      }
      setSelected([]);
    },
    [data]
  );

  const handleClick = useCallback(
    (event, id) => {
      const selectedIndex = selected.indexOf(id);
      let newSelected = [];

      if (selectedIndex === -1) {
        newSelected = newSelected.concat(selected, id);
      } else if (selectedIndex === 0) {
        newSelected = newSelected.concat(selected.slice(1));
      } else if (selectedIndex === selected.length - 1) {
        newSelected = newSelected.concat(selected.slice(0, -1));
      } else if (selectedIndex > 0) {
        newSelected = newSelected.concat(
          selected.slice(0, selectedIndex),
          selected.slice(selectedIndex + 1)
        );
      }

      setSelected(newSelected);
    },
    [selected]
  );

  // Mutliple page data display
  const handleChangePage = useCallback(
    (event, newPage) => {
      dispatch(changePage(newPage));
    },
    [dispatch]
  );

  const handleChangeRowsPerPage = useCallback(
    (event) => {
      const { value } = event.target;
      setRowsPerPage(parseInt(value, 10));
      dispatch(changePage(0));
    },
    [dispatch]
  );

  const renderCohortsColumn = useCallback(
    (userData) => {
      if (
        courses[selectedCourse] &&
        Object.keys(courses[selectedCourse].cohorts).length
      ) {
        return (
          <TableCell align="left" className={classes.dashedCell}>
            <Box component="div" className={classes.enrollmentStatus}>
              <SimpleSelect
                courses={courses}
                selectedCohort={
                  (userData.enrollments[selectedCourse] &&
                    userData.enrollments[selectedCourse].cohort.name) ||
                  ""
                }
                courseId={selectedCourse}
                user={userData}
              />
            </Box>
          </TableCell>
        );
      } else {
        return null;
      }
    },
    [classes.dashedCell, classes.enrollmentStatus, courses, selectedCourse]
  );

  const isSelected = useCallback(
    (name) => {
      return selected.indexOf(name) !== -1;
    },
    [selected]
  );

  const emptyRows =
    rowsPerPage -
    Math.min(rowsPerPage, Object.keys(data).length - page * rowsPerPage);

  const linKToTheUsersProfile = (userEmail) => {
    dispatch(fetchUserProfile(userEmail));
    dispatch(setNewNavigationValue(0));
    dispatch(setQuickSearchFieldValue(userEmail));
  }

  return (
    <div className={classes.root}>
      {isLoading ? (
        <Spinner />
      ) : !globalMode ? (
        <Fragment>
          <TabHeader />
          <EnhancedTableToolbar numSelected={selected.length} />
          <TableContainer className={classes.tableContainer}>
            <Box className={classes.courseViewSelectorClass}>
              <SearchBar data={data} />
              {courses[selectedCourse] &&
                Object.values(courses[selectedCourse].cohorts).length > 0 && (
                  <RenderCohortsList
                    courses={courses}
                    selectedEnrollments={selectedEnrollments}
                    selectedCourseId={selectedCourse}
                  />
                )}
            </Box>
            <Table
              className={classes.table}
              aria-labelledby="tableTitle"
              size="medium"
              aria-label="enhanced table"
            >
              <EnhancedTableHead
                classes={classes}
                numSelected={selected.length}
                order={order}
                orderBy={orderBy}
                onSelectAllClick={handleSelectAllClick}
                onRequestSort={handleRequestSort}
                rowCount={Object.keys(data).length}
                courses={courses}
                selectedCourseId={selectedCourse}
                usersSelection={selected}
                data={data}
              />
              {showSuggestions ? (
                <TableBody>
                  {stableSort(
                    Object.values(data),
                    getComparator(order, orderBy)
                  )
                    .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                    .map((userData, index) => {
                      const isItemSelected = isSelected(userData.id);
                      const labelId = `enhanced-table-checkbox-${index}`;
                      return (
                        <TableRow
                          hover
                          className={classes.pointer} 
                          onClick={
                            (event) => handleClick(event, userData.id),
                            () => linKToTheUsersProfile(userData.email)
                          }
                          role="checkbox"
                          aria-checked={isItemSelected}
                          tabIndex={-1}
                          key={userData.id}
                          selected={isItemSelected}
                        >
                          <TableCell padding="checkbox">
                            <Checkbox
                              checked={isItemSelected}
                              inputProps={{ "aria-labelledby": labelId }}
                            />
                          </TableCell>
                          <TableCell component="th" scope="row" padding="none">
                            {userData.is_active ? "Active" : "Never connected"}
                          </TableCell>
                          <TableCell align="left">
                            <Typography variant="subtitle2">
                              {userData.last_name}
                            </Typography>
                            <Typography variant="caption">
                              {userData.first_name}
                            </Typography>
                          </TableCell>
                          <TableCell align="left" id={labelId}>
                            <Typography variant="subtitle2">
                              {userData.username}
                            </Typography>
                            {/* <Typography className={classes.pointer} onClick={() => linKToTheUsersProfile(userData.email)} variant="caption"> */}
                            <Typography variant="caption">
                              {userData.email}
                            </Typography>
                          </TableCell>
                          <TableCell
                            align="left"
                            className={classes.dashedCell}
                          >

                          <EnrollmentStatus userData={userData} />

                          </TableCell>
                          {renderCohortsColumn(userData)}
                        </TableRow>
                      );
                    })}
                  {emptyRows > 0 && (
                    <TableRow style={{ height: 53 * emptyRows }}>
                      <TableCell colSpan={6} />
                    </TableRow>
                  )}
                </TableBody>
              ) : (
                <TableBody>
                  <TableRow hover role="checkbox" tabIndex={-1}>
                    <TableCell padding="checkbox"></TableCell>
                    <TableCell></TableCell>
                    <TableCell align="left"></TableCell>
                    <TableCell align="left"></TableCell>
                    <TableCell
                      align="left"
                      className={classes.dashedCell}
                    ></TableCell>
                  </TableRow>
                </TableBody>
              )}
            </Table>
          </TableContainer>
          <TablePagination
            rowsPerPageOptions={[10]}
            component="div"
            count={Object.keys(data).length}
            rowsPerPage={rowsPerPage}
            page={page}
            onChangePage={handleChangePage}
            onChangeRowsPerPage={handleChangeRowsPerPage}
          />
        </Fragment>
      ) : (
        <GlobalEnrollmentsView />
      )}
    </div>
  );
};

export default DataTable;
