import React, { Fragment, useCallback, useEffect } from "react";
import { useSelector } from "react-redux";
import { useDispatch } from "react-redux";
import { makeStyles } from "@material-ui/core/styles";
import CustomTabs from "../navigation/CustomTabs";
import TabPanel from "../navigation/TabPanel";
import Main from "./main/Main";
import EnrollmentsView from "./EnrollmentsView";
import EmailEditorWrapper from "./email-sender/EmailEditorWrapper";
import {
  fetchEnrollments,
  getCourseEnrollmentsCount,
  fetchCoursesList,
  setTabIndex,
} from "../../store/actions";
import ImportModeSelector from "./import-students/ImportModeSelector"

const useStyles = makeStyles((theme) => ({
  nav: {
    backgroundColor: theme.palette.background.main,
    paddingBottom: "15px",
  }
}));

const DataRenderer = () => {
  const classes = useStyles();

  const {
    trad,
    configuration,
    isSmallApp,
    displayGlobalMode,
    tab,
  } = useSelector((state) => ({
    trad: state.translations.values,
    configuration: state.configuration.values,
    isSmallApp: state.userManagement.isSmallApp,
    displayGlobalMode: state.userManagement.displayGlobalMode,
    tab: state.userManagement.tab,
  }));

  const dispatch = useDispatch();

  const handleChangeTab = useCallback(
    (event, newValue) => {
      dispatch(setTabIndex(newValue));
    },
    [dispatch]
  );

  //TODO factoriser
  const fetchAllEnrollments = useCallback(async () => {
    await dispatch(fetchEnrollments());
  }, [dispatch]);

  const fetchAllCourses = useCallback(async () => {
    await dispatch(fetchCoursesList());
  }, [dispatch]);

  const getEnrollmentsCount = useCallback(async () => {
    await dispatch(getCourseEnrollmentsCount());
  }, [dispatch]);

  useEffect(() => {
    getEnrollmentsCount();
  }, [getEnrollmentsCount]);

  useEffect(() => {
    if (isSmallApp !== undefined) {
      if (isSmallApp) {
        fetchAllEnrollments();
      } else {
        fetchAllCourses();
      }
    }
  }, [isSmallApp, fetchAllCourses, fetchAllEnrollments]);

  let navigationTab = [trad.main]

  if (!configuration || !configuration.DISPLAY_STATUS) {
    navigationTab = [trad.main, trad.view_enrollment, trad.import, trad.send_mail]
  } else {
    if (configuration.DISPLAY_STATUS.DISPLAY_VIEW_ENROLLMENT) {
      navigationTab.push(trad.view_enrollment)
    }
    if (configuration.DISPLAY_STATUS.DISPLAY_IMPORT) {
      navigationTab.push(trad.import)
    }
    if (configuration.DISPLAY_STATUS.DISPLAY_SEND_MAIL) {
      navigationTab.push(trad.send_mail)
    }
  }

  return (
    <Fragment>
      <div className={classes.nav}>
        <CustomTabs
          className={classes.nav}
          value={tab}
          handleChangeTab={handleChangeTab}
          tabsList={
            !isSmallApp && displayGlobalMode
              ? [trad.main]
              : isSmallApp && displayGlobalMode
              ? [trad.main, trad.view_enrollment]
              : navigationTab
          }
        />
      </div>

      <TabPanel value={tab} index={navigationTab.indexOf(trad.main)}>
        <Main />
      </TabPanel>
      <TabPanel value={tab} index={navigationTab.indexOf(trad.view_enrollment)}>
        <EnrollmentsView />
      </TabPanel>
      <TabPanel value={tab} index={navigationTab.indexOf(trad.import)}>
        <ImportModeSelector  />
      </TabPanel>
      <TabPanel value={tab} index={navigationTab.indexOf(trad.send_mail)}>
        <EmailEditorWrapper />
      </TabPanel>
    </Fragment>
  );
};

export default DataRenderer;
