import React, { useRef } from "react";
import { convertToTarfile } from "../store/actions";
import { useDispatch, useSelector } from "react-redux";
import { setConverterCourseTitle, setDegreeOfCert } from "../store/actions";

import { Grid, Typography } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import CircularProgress from '@material-ui/core/CircularProgress';

const OpenedXConverter = () => {

  // Redux store setup
  const { trad, courseTitle, degreeOfCert,displaySpinner, displayTarfileLink, displayErrorMessage } = useSelector(
    (state) => ({
      trad: state.translations.values,
      courseTitle: state.openedXConverter.courseTitle,
      degreeOfCert: state.openedXConverter.degreeOfCert,
      displaySpinner: state.openedXConverter.displaySpinner,
      displayTarfileLink: state.openedXConverter.displayTarfileLink,
      displayErrorMessage: state.openedXConverter.displayErrorMessage
    })
  );

  const dispatch = useDispatch();

  const handleCourseTitle = (event) => {
    dispatch(setConverterCourseTitle(event))
  }

  const handleDegreeOfCert = (event) => {
    
    dispatch(setDegreeOfCert(event))
  }

  // const buttonRef = React.createRef();

  const inputFile = useRef(null) 
  
  const handleSubmit = () => {

    let excelFile = inputFile.current.files[0]

    if (!excelFile || !courseTitle) {
      alert(trad.converter_alert_1)
    } else if (excelFile["name"].indexOf(".xlsx") === -1) {
      alert (trad.converter_alert_2)
    } else  {
      dispatch(convertToTarfile(degreeOfCert, courseTitle, excelFile));
    };

  }

  const classes = useStyles();

  return (
    <Grid container >
      <Grid item xs={1}></Grid>
      <Grid
        item
        xs={10}
      >
        <Typography color="inherit" className={classes.instruction} component="div">
            {trad.title_converter}
          <ol className={classes.list}>
            <li>{trad.converter_instruct_step_1}</li>
            <li>{trad.converter_instruct_step_2}</li>
            <li>{trad.converter_instruct_step_3}</li>
          </ol>
        </Typography>
      </Grid>
      <Grid item xs={1}></Grid>

      <Grid item xs={1}></Grid>
      <Grid
        item
        xs={10}
        container
        className={classes.dataArea}  
      >
        <Grid 
          item xs={12} 
          container 
          direction="column"
        >
          <Grid container>
            <Grid item xs={6} className={classes.col1}>
              <label htmlFor="inputFile">{trad.select_file_here}</label>  
            </Grid>
            <Grid item xs={6} className={classes.col2}>
              <input 
                type='file' 
                id='inputFile' 
                name='inputFile' 
                ref={inputFile}
                className={classes.button} 
              />
            </Grid>
          </Grid>
          <Grid container>
            <Grid item xs={6} className={classes.col1} >
              <label htmlFor="courseTitle">{trad.enter_course_name}</label>
            </Grid>
            <Grid item xs={6} className={classes.col2}>
              <input 
                id='courseTitle'
                value={courseTitle}
                label="courseTitle"
                placeholder="Course Title"
                onChange={(event) => handleCourseTitle(event.target.value)}
                className={classes.input}
              />
            </Grid>
          </Grid>
          <Grid container>
            <Grid item xs={6} className={classes.col1}>
              <label htmlFor="degreeOfCert">{trad.check_for_DoC}</label>
            </Grid>
            <Grid item xs={6} className={classes.col2}>
              <input 
                id="degreeOfCert"
                type="checkbox" 
                defaultChecked={degreeOfCert}
                onChange={()=>handleDegreeOfCert(degreeOfCert? false : true)} 
                // checked={degreeOfCert} 
              />
            </Grid>
          </Grid>

        </Grid >
        
      </Grid>
      <Grid item xs={1}></Grid>

      <Grid item xs={5}></Grid>
      <Grid item xs={2}>      
        <Button
          className={classes.submit}
          onClick={handleSubmit}
          variant="contained"
          color="primary"
          component="span"
        >
          {trad.start_convert}
        </Button>
      </Grid>
      <Grid item xs={5}></Grid>

      <Grid item xs={12} className={displaySpinner ? classes.centered : classes.hidden } >
        <CircularProgress />  
      </Grid>


      <Grid className={!displayTarfileLink && classes.hidden} container>
        <Grid item xs={3}></Grid>
        <Grid item xs={6} className={classes.centered}>
  
            <a
              className={ classes.download}
              href="/media/microsite/nuclear-training-program/wul_dashboard/course.tar.gz"
              download="course.tar.gz"
            >
              {trad.download_targz}
            </a>
              
        </Grid>
        <Grid item xs={3}></Grid>
      </Grid>

      <Grid className={!displayErrorMessage && classes.hidden} container>
        <Grid item xs={3}></Grid>
        <Grid item xs={6} className={classes.centered}>

          <span>
            {displayErrorMessage}
          </span>

        </Grid>
        <Grid item xs={3}></Grid>
      </Grid>

    </Grid>
    );
}


const useStyles = makeStyles((theme) => ({
  root: {
    width: "100%",
  },
  instruction : {
    borderRadius: 15,
    marginTop: 100,
    marginBottom: 50,
    backgroundColor: "white",
    fontSize: 15,
    fontWeight: "bold",
    padding: 30,
    textAlign: "center"
  },
  list : {
    textAlign: "left",
    marginLeft: "23%",
    marginTop: 30
  },
  dataArea : {
    backgroundColor: "white",
    padding: 30,
    borderRadius: 15,
  },
  centered : {
    display: "flex",
    alignItems: "center",
    justifyContent : "center",
    marginTop: 40
  },
  col1 : {
    display: "flex",
    alignItems: "center",
    justifyContent : "flex-end",
    paddingRight: 20 
  },
  col2 : {
    display: "flex",
    alignItems: "center",
    justifyContent : "flex-start",
    paddingLeft: 20 
  },
  input: {
    backgroundColor: "white",
    height: 40,
    margin: 30,
    marginLeft: 0,
    borderRadius: 5,
    padding:10, 
  },
  fileSpan : {
    borderColor : "black",
    borderStyle : "solid",
    borderWidth : 1,
    borderRadius : 5,
    padding : 5,
    marginLeft:15,
    width : 250,
    textAlign: "center"
  },
  browseArea : {
    display: "flex",
    justifyContent: "center",
    alignItems: "center"
  },
  submit : {
    marginTop: 40,
    width: "100%"
  },
  download : {
    marginBottom: 45,
    color: "#2C9CDB",
    textAlign: "center",
    borderRadius: 3,
    fontSize: "1.5rem",
    fontWeight: "bold"
  },
  hidden : {
    display: "none"
  }
}));


export default OpenedXConverter;