import React from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import TextField from '@material-ui/core/TextField';
import { useDispatch, useSelector } from "react-redux";
import  {closeTimeTrackingModal, updateUserTimeTracking, getSecondsToAddByHours, getSecondsToAddByMinutes} from "../../../store/actions";
import Grid from '@material-ui/core/Grid';

const TimeTrackingModal = () => {
  const dispatch = useDispatch();

  const {timeTrackingModalOpen,trad, timeTrackingUser, secondsToaddToUserCourseByHours, secondsToaddToUserCourseByMinutes, userProfile} = useSelector((state) => ({
      timeTrackingUser : state.quickSearch.timeTrackingUser,
      trad: state.translations.values,
      timeTrackingModalOpen: state.quickSearch.timeTrackingModalOpen,
      secondsToaddToUserCourseByHours: state.quickSearch.secondsToaddToUserCourseByHours,
      secondsToaddToUserCourseByMinutes: state.quickSearch.secondsToaddToUserCourseByMinutes,
      userProfile: state.quickSearch.userProfile
      }));

  const userEmail = userProfile.email
  const totalSecondsToAdd  = secondsToaddToUserCourseByHours + secondsToaddToUserCourseByMinutes;
  const handleClose = () => {
    dispatch(closeTimeTrackingModal())
  };

  const addTimeForUser = () => {
    dispatch(updateUserTimeTracking(timeTrackingUser, totalSecondsToAdd, userEmail))
  };

  const handleChangeHours = (event) => {
    dispatch(getSecondsToAddByHours(parseInt(event.target.value, 10) * 3600))
 }

 const handleChangeMinutes = (event) => {
    dispatch(getSecondsToAddByMinutes(parseInt(event.target.value, 10) * 60))
 }

  return (
    <Grid>
      <Dialog
        open={timeTrackingModalOpen}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogContent>
            <DialogContentText id="alert-dialog-description">
              {trad.add_time_to_user}
            </DialogContentText>
        </DialogContent>

        <Grid container >
          <Grid item xs={1}></Grid>
          <Grid item xs={4}>
            <TextField
              id="outlined-number"
              label="Heures"
              type="number"
              InputLabelProps={{
                shrink: true,
              }}
              InputProps={{ inputProps: { min: 0, max: 100} }}
              variant="outlined"
              onChange={handleChangeHours}
              fullWidth
            />
          </Grid >
          <Grid item xs={2}></Grid>
          <Grid item xs={4}>
            <TextField
              id="outlined-number"
              label="Minutes"
              type="number"
              InputLabelProps={{
                shrink: true,
              }}
              InputProps={{ inputProps: { min: 0, max: 60 } }}
              variant="outlined"
              onChange={handleChangeMinutes}
              fullWidth
            />
          </Grid>
          <Grid item xs={1}></Grid>
        </Grid>
        
        <DialogActions>
          <Button onClick={addTimeForUser} color="primary">
            {trad.add}
          </Button>
          <Button onClick={handleClose} color="primary">
            {trad.cancel}
          </Button>
        </DialogActions>
      </Dialog>
    </Grid>
  );
}

export default TimeTrackingModal;