import {
  FETCH_USER_PROFILE,
  SET_PROFILE_LOADER,
  UPDATE_TIME_STAMPS,
  SAVE_CUSTOM_FIELDS,
  UPDATE_CUSTOM_FIELDS,
  GENERATE_NEW_PASSWORD_LINK,
  SET_QUICKESEARCH_FIELD_VALUE,
  OPEN_TIME_TRACKING_MODAL,
  CLOSE_TIME_TRACKING_MODAL,
  UPDATE_USER_TIME_TRACKING,
  GET_SECONDS_TO_ADD_BY_HOURS,
  GET_SECONDS_TO_ADD_BY_MINUTES
} from "../actions/types";

const INITIAL_STATE = {
  userProfile: {},
  customFields: {},
  loadingProfile: false,
  searchMade: false,
  snackSettings: {},
  customFieldSaveOn: false,
  passwordLink: "",
  userMail: "",
  timeTrackingModalOpen:false,
  timeTrackingUser:"",
  secondsToaddToUserCourseByHours: 0,
  secondsToaddToUserCourseByMinutes: 0
};

const quickSearchReducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case FETCH_USER_PROFILE:
      return {
        ...state,
        userProfile: action.payload,
        searchMade: true,
        loadingProfile: false,
        passwordLink: "",
      };

    case SET_PROFILE_LOADER:
      return {
        ...state,
        userProfile: {},
        searchMade: false,
        loadingProfile: true,
      };

    case UPDATE_TIME_STAMPS:
      return {
        ...state,
        userProfile: {
          ...state.userProfile,
          custom_field: {
            ...state.userProfile.custom_field,
            [action.payload.courseId]: action.payload.date,
          },
        },
        customFieldSaveOn: true,
      };
    case UPDATE_CUSTOM_FIELDS:
      const { config, name, value } = action.payload;
      return {
        ...state,
        userProfile: {
          ...state.userProfile,
          [config]: {
            ...state.userProfile[config],
            [name]: value,
          },
        },
        customFieldSaveOn: true,
      };
    case SAVE_CUSTOM_FIELDS:
      return {
        ...state,
        snackSettings: action.payload.snackSettings,
        customFieldSaveOn: false,
      };
    case GENERATE_NEW_PASSWORD_LINK:
      return {
        ...state,
        passwordLink: action.payload,
      };
    case SET_QUICKESEARCH_FIELD_VALUE :
      return {
        ...state,
        userMail: action.payload
      };
    case OPEN_TIME_TRACKING_MODAL :
      return {
        ...state,
        timeTrackingModalOpen:true,
        timeTrackingUser: action.payload
      };
    case CLOSE_TIME_TRACKING_MODAL :
      return {
        ...state,
        timeTrackingModalOpen:false
      };
    case UPDATE_USER_TIME_TRACKING :
      const { courseId, secondsToAdd } = action.payload;
      return {
        ...state,
        userProfile:{
          ...state.userProfile,
          enrollments:{
            ...state.userProfile.enrollments,
            [courseId]: {
              ...state.userProfile.enrollments[courseId],
              time_tracking: state.userProfile.enrollments[courseId].time_tracking.split(':').reduce((acc,time) => (60 * acc) + +time) + secondsToAdd
            }
          },
        },
        timeTrackingModalOpen:false,
        secondsToaddToUserCourseByHours: 0,
        secondsToaddToUserCourseByMinutes: 0
      };
    case GET_SECONDS_TO_ADD_BY_HOURS :
      return {
        ...state,
        secondsToaddToUserCourseByHours:action.payload
      }
    case GET_SECONDS_TO_ADD_BY_MINUTES :
      return {
        ...state,
        secondsToaddToUserCourseByMinutes:action.payload
      }
  
    default:
      return state;
  }
};
export default quickSearchReducer;