import React, { Fragment } from "react";
import { useSelector } from "react-redux";
import { Grid, CardMedia } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import MainCourse from "./MainCourse";
import MainGlobal from "./MainGlobal";
import Spinner from "../../navigation/Spinner";
import Typography from "@material-ui/core/Typography";

import logo from "../../../../src/assets/img/logo.png";

const useStyles = makeStyles((theme) => ({
  container: {
    backgroundColor: theme.palette.background.main,
    marginTop: "-20px",
  },
  containerTop: {

    margin: "10px",
  },
  gridTop: {
    padding: "20px 0px 20px 80px",
    borderRadius: "5px",
  },

  gridBottom: {
    padding: "10px",
    margin: "10px",
    width: "46%",
    borderRadius: "5px",
    [theme.breakpoints.down(1610)]: {
      width: "100%",
    },
  },
  image: {
    width: "200px",
  },
  altImage: {
    width: "200px",
  },
  title: {
    marginBottom: "1em",
    maxWidth: "200px"
  },
  flexContainer: {
    flexGrow: 1,
  },
}));

const Main = () => {
  const {
    selectedCourse,
    courses,
    globalMode,
    isLoading,
    trad,
  } = useSelector((state) => ({
    selectedCourse: state.userManagement.selectedCourse,
    globalMode: state.userManagement.displayGlobalMode,
    courses: state.userManagement.courses,
    isLoading: state.userManagement.isLoading,
    trad: state.translations.values,
  }));

  const classes = useStyles();


  // a fonction to test if the course_image_url is not a 404
  const [didLoad, setLoad] = React.useState(false);

  const checkImageUrl = (src) => {
    const style = didLoad ? { width: "200px" } : { visibility: "hidden" };
    return (
      <img
        style={style}
        src={src}

        alt="course logo"

        onLoad={() => setLoad(true)}
      />
    );
  };

  if (isLoading) {
    return <Spinner />;
  } else if (!globalMode) {
    return (
      <Fragment>

        <Grid
          item
          xs={12}
          className={classes.gridTop}
          container
          direction="row"
          alignItems="center"
          justify="flex-end"
        >
          <Grid className={classes.flexContainer}>
            <Typography className={classes.title}>
              {courses[selectedCourse].display_name}
            </Typography>

            {process.env.NODE_ENV !== "development" ? (
              <div>
                {checkImageUrl(courses[selectedCourse].image_url)}
                {!didLoad && (
                  <CardMedia
                    className={classes.image}
                    component="img"
                    alt={trad.course_logo}
                    //rcuprer l'image dans static
                    image="https://weuplearning.themoocagency.com/wp-content/uploads/sites/8/2019/05/Logo_WeUp_1@6xmarge-horizon.png"
                    title={trad.course_logo}
                  />
                )}
              </div>
            ) : (
              <CardMedia
                className={classes.image}
                component="img"
                alt={trad.course_logo}
                image={logo}
                title={trad.course_logo}

              />
            )}
          </Grid>

          <MainCourse
            courseId={courses[selectedCourse]}
            trad={trad}
            selectedCourse={selectedCourse}
          />
        </Grid>
      </Fragment>
    );
  } else {
    return <MainGlobal courses={courses} trad={trad} />;
  }
};

export default Main;