import React from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import { useDispatch, useSelector } from "react-redux";
import { closeModal} from "../../store/actions";

export default function AlertDialog() {
  const dispatch = useDispatch();

  const { messageModalOpen, generatedGradeReportMessage, trad} = useSelector((state) => ({
      generatedGradeReportMessage : state.userManagement.generatedGradeReportMessage,
      messageModalOpen: state.userManagement.messageModalOpen,
      trad: state.translations.values,
      }));

  const handleClose = () => {
    dispatch(closeModal())
  };

  return (
    <div>
      <Dialog
        open={messageModalOpen}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogContent>
          {generatedGradeReportMessage 
          ?
          <DialogContentText id="alert-dialog-description">
            {generatedGradeReportMessage}
          </DialogContentText>
          : 
          <DialogContentText id="alert-dialog-description">
            {trad.work_running}
          </DialogContentText>
          }
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            {trad.understand}
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
