import React from "react";
import { useDispatch } from "react-redux";
import { makeStyles } from "@material-ui/core/styles";
import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";

import { updateStudentCohort } from "../../store/actions";

const useStyles = makeStyles((theme) => ({
  formControl: {
    margin: theme.spacing(1),
    minWidth: 180,
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
}));

const SimpleSelect = ({ courseId, selectedCohort, user, courses }) => {
  const dispatch = useDispatch();

  const changeUserCohort = (event, userId, newCohort, courseId, userEmail) => {
    let cohortObject = {};
    Object.entries(courses[courseId].cohorts).forEach((cohort) => {
      if (newCohort === cohort[1]) {
        cohortObject = {
          id: cohort[0],
          name: newCohort,
        };
      }
    });

    dispatch(
      updateStudentCohort(event, userId, cohortObject, courseId, userEmail)
    );
  };

  const cohorts = Object.entries(courses[courseId].cohorts);

  const classes = useStyles();

  return (
    <div>
      <FormControl variant="outlined" className={classes.formControl}>
        <Select
          labelId="demo-simple-select-outlined-label"
          id="demo-simple-select-outlined"
          value={selectedCohort}
          onChange={(event) => {
            changeUserCohort(
              event,
              user.id,
              event.target.value,
              courseId,
              user.email
            );
          }}
          onClick={(event) => {
            event.stopPropagation();
          }}
        >
          {cohorts.map((cohort) => (
            <MenuItem key={cohort[0]} value={cohort[1]}>
              {cohort[1]}
            </MenuItem>
          ))}
        </Select>
      </FormControl>
    </div>
  );
};

export default SimpleSelect;
