import React from "react";
import { useSelector } from "react-redux";
import { makeStyles, Grid } from "@material-ui/core/";

const useStyles = makeStyles((theme) => ({
  tabTitle: {
    backgroundColor: theme.palette.primary.main,
    color: "white",
    padding: "8px 16px",
    textTransform: "uppercase",
    fontWeight: 700,
    fontSize: "16px",
  },
}));

const TabHeader = () => {
  const classes = useStyles();
  const { selectedCourse, globalMode, courses } = useSelector((state) => ({
    selectedCourse: state.userManagement.selectedCourse,
    globalMode: state.userManagement.displayGlobalMode,
    courses: state.userManagement.courses,
  }));
  return (
    <Grid className={classes.tabTitle}>
      {globalMode ? "global mode" : courses[selectedCourse].display_name}
    </Grid>
  );
};
export default TabHeader;
