import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import { useSelector } from "react-redux";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import { Box } from "@material-ui/core";
import moment from "moment";
import 'moment/locale/fr'  

const useStyles = makeStyles({
  table: {
    minWidth: 650,
  },
});



const UserTable = () => {
  const classes = useStyles();
  const { userProfile, trad } = useSelector((state) => ({
    userProfile: state.quickSearch.userProfile,
    trad: state.translations.values,
  }));


  const lastLoginDate = moment(userProfile.last_login).format("lll")
  const frenchFormatLastLoginDate = lastLoginDate.replace("-", "");

  const inscriptionDate = moment(userProfile.inscription).format("lll")
  const frenchFormatInscriptionDate = inscriptionDate.replace("-", "");


  let totalCourseEnrollment = 0
  Object.values(userProfile.enrollments).forEach(( course => {
    if (course.enrolled_to_enrollment) {
      totalCourseEnrollment++
    } 
  }))

  return (
    <TableContainer component={Paper}>
      <Table className={classes.table} aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell align="center">
              <Box fontWeight={700}>{trad.status}</Box>
            </TableCell>
            <TableCell align="center">
              <Box fontWeight={700}>{trad.last_name}</Box>
            </TableCell>
            <TableCell align="center">
              <Box fontWeight={700}>{trad.first_name}</Box>
            </TableCell>
            <TableCell align="center">
              <Box fontWeight={700}>{trad.username}</Box>
            </TableCell>
            <TableCell align="center">
              <Box fontWeight={700}>{trad.last_login}</Box>
            </TableCell>
            <TableCell align="center">
              <Box fontWeight={700}>{trad.created}</Box>
            </TableCell>
            <TableCell align="center">
              <Box fontWeight={700}>{trad.total_course_enrollment}</Box>
            </TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          <TableRow>
            <TableCell align="center">
            {userProfile.active ? trad.active : trad.inactive}
            </TableCell>
            <TableCell align="left">{userProfile.last_name}</TableCell>
            <TableCell align="left">{userProfile.first_name}</TableCell>
            <TableCell align="center">{userProfile.username}</TableCell>
            <TableCell align="center">{frenchFormatLastLoginDate}</TableCell>
            <TableCell align="center">{frenchFormatInscriptionDate}</TableCell>
            <TableCell align="center">
              {totalCourseEnrollment}
            </TableCell>
          </TableRow>
        </TableBody>
      </Table>
    </TableContainer>
  );
};

export default UserTable;
