import React, { useCallback, useState, useEffect } from "react";
import { useSelector } from "react-redux";
import { useDispatch } from "react-redux";
import { setEmailContent } from "../../../store/actions";
import { makeStyles, Typography, Checkbox, Grid } from "@material-ui/core/";

const useStyles = makeStyles((theme) => ({
  recipientItem: {
    display: "inline-block",
    textTransform: "capitalize",
  },
  editor: {
    padding: "8px 16px",
    fontSize: "12px",
    fontWeight: 700,
    marginBottom: 2,
    color: theme.palette.primary.main,
    textTransform: "uppercase",
  },
}));

// RECIPIENTS BLOCK COMPONENTS

const RecipientItem = ({ emailContent, recipient, cohort }) => {
  const classes = useStyles();
  const dispatch = useDispatch();
  const { recipientItem } = classes;
  const { recipients, cohortsRecipients } = emailContent;

  const [checked, setCheck] = useState(false);

  const handleSelect = useCallback(
    (event, selectedRecipient) => {
      const { name } = event.target;
      let newRecipients = [];

      if(cohort) {
        newRecipients = [...cohortsRecipients];
      } else {
        newRecipients = [...recipients]
      }
      
      let index = newRecipients.indexOf(selectedRecipient)

      if(index !== -1){
        newRecipients.splice(index, 1)
      } else {
        newRecipients.push(selectedRecipient)
      }
      dispatch(setEmailContent(name, newRecipients));
    },
    [recipients, dispatch, cohort, cohortsRecipients]
  );

  useEffect(() => {
    if(!cohort){
      recipients.indexOf(recipient) !== -1 ? setCheck(true) : setCheck(false);
    } else {
      cohortsRecipients.indexOf(recipient) !== -1 ? setCheck(true) : setCheck(false);
    }
  }, [cohortsRecipients, recipients, recipient, cohort]);


  return (
    <Grid key={recipient} item>
      <Checkbox
        color="primary"
        name={cohort ? "cohortsRecipients" : "recipients"}
        checked={checked}
        onChange={(event) => handleSelect(event, recipient)}
      />
      <Typography className={recipientItem}>{recipient}</Typography>
    </Grid>
  );
};

const RecipientsBlock = ({ emailContent, cohorts}) => {
  const { trad } = useSelector((state) => ({
    trad: state.translations.values,
  }));
  
  const classes = useStyles();
  const { editor } = classes;
  let recipientList = ["myself", "staff", "all"];
  let cohortsList = Object.keys(cohorts)
  return (
    <Grid item xs={10}>
      <Typography variant="h6" component="div" className={editor}>
        {trad.recipient}
      </Typography>
      <Grid container spacing={3}>
        {recipientList.map((recipient) => {
          return (
            <RecipientItem
              key={recipient}
              emailContent={emailContent}
              recipient={recipient}
            />
          );
        })}
        {cohortsList.map((recipient) => {
          return (
            <RecipientItem
              key={recipient}
              emailContent={emailContent}
              recipient={recipient}
              cohort={true}
            />
          );
        })}
      </Grid>
    </Grid>
  );
};

export default RecipientsBlock;
