import {
    DISPLAY_TARFILE_LINK,
    SET_CONVERTER_COURSE_TITLE,
    SET_DEGREE_OF_CERTAINTY,
    DISPLAY_SPINNER
} from "../actions/types";

const INITIAL_STATE = {
    courseTitle: "",
    degreeOfCert: false,
    displayTarfileLink : false,
    displaySpinner : false,
    displayErrorMessage : false,
}

const openedXConverterReducer = (state = INITIAL_STATE, action ) => {

    switch (action.type) {
        case SET_CONVERTER_COURSE_TITLE:
            return {
                ...state,
                courseTitle: action.payload,
            };

        case SET_DEGREE_OF_CERTAINTY:
            return {
                ...state,
                degreeOfCert: action.payload,
            };

        case DISPLAY_SPINNER:
            return {
                ...state,
                displaySpinner: action.payload.displaySpinner,
                displayErrorMessage: false,
                displayTarfileLink : false
            };

        case DISPLAY_TARFILE_LINK:
            return {
                ...state,
                displaySpinner: false,
                displayErrorMessage: action.payload.displayErrorMessage,
                displayTarfileLink: action.payload.displayTarfileLink
            };

        default:
            return state;
    }
};
export default openedXConverterReducer;