import React, { useCallback } from "react";
import { Checkbox } from "@material-ui/core";

const EditorCheckbox = ({ value, handleFieldChange }) => {

  if (value === "false") {
    value = false
  } else if (value === "true") {
    value = true
  }

  const handleChange = useCallback(
    (event) => {
      handleFieldChange(event.target.checked);
    },
    [handleFieldChange]
  );

  return <Checkbox checked={value} onChange={handleChange} color="primary" />;
};
export default EditorCheckbox;
