import React, { Fragment, useEffect, useCallback } from "react";
import { useSelector, useDispatch } from "react-redux";
import { makeStyles } from "@material-ui/core/styles";
import Spinner from "../../navigation/Spinner";
import Typography from "@material-ui/core/Typography";
import TextField from "@material-ui/core/TextField";
import { Grid } from "@material-ui/core";
// import CohortSelector from './CohortSelector'
import Button from "@material-ui/core/Button";
import EnrollmentTable from "./EnrollmentTable";
import CheckDataButton from "./CheckDataButton";
import { readString } from "react-papaparse";
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';

import {
  getEnrollFieldValue,
  getRegisterFieldsValue,
  importUsersFromCSV,
  isTableDisplayed,
  formatEnrollFieldInArray,
  updateImportTable
} from "../../../store/actions";

const useStyles = makeStyles((theme) => ({
  button: {
    margin: "20px",
  },
  input :{
    width: "99%"
  },
  dataMissing : {
    color: "red",
    textAlign: "center",
    marginTop: "10px"
  },
  wrongEmail : {
    color: "red",
    textAlign: "center",
    marginTop: "10px"
  },
  informations : {
    textAlign: "center",
    marginTop: "10px"
  },
  importSuccess : {
    color: "green",
    textAlign: "center",
    marginTop: "10px"
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
  selectorContainer: {
    display: "flex",
    alignItems: "baseline"
  }

}));

const EnrollUnenroll = () => {
  const {
    enrollFieldValue,
    selectedCourse,
    globalMode,
    isLoading,
    isTableOpened,
    enrollFieldArray,
    registerFieldsValue,
    importUsersSuccess,
    selectedCohortForImport,
    trad
  } = useSelector((state) => ({
    enrollFieldValue: state.userManagement.enrollFieldValue,
    selectedCourse: state.userManagement.selectedCourse,
    globalMode: state.userManagement.displayGlobalMode,
    isLoading: state.userManagement.isLoading,
    isTableOpened: state.userManagement.isTableOpened,
    enrollFieldArray: state.userManagement.enrollFieldArray,
    registerFieldsValue: state.userManagement.registerFieldsValue,
    importUsersSuccess: state.userManagement.importUsersSuccess,
    selectedCohortForImport: state.userManagement.selectedCohortForImport,
    trad: state.translations.values,
  }));

const dispatch = useDispatch();
const classes = useStyles();

const onInputChange = (inputValue) => {
  dispatch(getEnrollFieldValue(inputValue));
};

const getValuesFromRegisterField = useCallback(async () => {
  await dispatch(getRegisterFieldsValue());
}, [dispatch]);

useEffect(() => {
  getValuesFromRegisterField();
}, [getValuesFromRegisterField]);

const importUsers = () => {
  dispatch(importUsersFromCSV(importedUsers, selectedCourse, selectedCohortForImport));

};


//Method to transform the object array in string for the placeholder
let registerFieldsValueArray;
let arrayHeader;
let arrayHeaderForPlaceHolder;
let fieldsForPlaceHolder;

if (registerFieldsValue.register_fields !== undefined) {
  registerFieldsValueArray = Object.values(registerFieldsValue.register_fields);
  // case when some field need to be displayed in the custom field editor but not in the import section
  const undisplayedFieldsArray = registerFieldsValueArray.filter((field) => !field.undisplay)
  arrayHeader = undisplayedFieldsArray.map((field) => field.name);

  arrayHeaderForPlaceHolder = undisplayedFieldsArray.map((field) => field.label);
  fieldsForPlaceHolder = arrayHeaderForPlaceHolder.toString();

}

//method to transform 2 array in one array of object to send data in correct format with key/values
const rows = enrollFieldArray.data
let importedUsers
let importedUsersWithLabel

if (rows) {
    importedUsers=rows.map( (innerArray) =>{let obj={};innerArray.forEach( (innerData,index) =>{obj[arrayHeader[index]]=innerData;});return obj;
  });
  importedUsersWithLabel=rows.map( (innerArray) =>{let obj={};innerArray.forEach( (innerData,index) =>{obj[arrayHeaderForPlaceHolder[index]]=innerData;});return obj;
});
}

//method to chck if all requiered field are completed
let dataMissing = false
let requiredField
if (registerFieldsValueArray) {
  requiredField = registerFieldsValueArray.filter(element => element.required === true )
}

if (requiredField && importedUsers) {
  requiredField.forEach((field) => {
    const label = field.name
    importedUsers.forEach((user) =>{
      if (!user[label]) {
        dataMissing = true
      }
    })
  })
}


//check if email format is correct
const validateEmail = (mail) =>
{
 if (/^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/.test(mail))
  {
    return (true)
  }
    return (false)
}

let emailError = []
if (importedUsers){
  importedUsers.forEach((user => {
    if (!validateEmail(user.email)) {
      emailError.push(user.email)
    }
  }))
}

// check if fields with option get the correct data

let fieldsWithOptions
if (registerFieldsValueArray) {
  fieldsWithOptions = registerFieldsValueArray.filter(element => element.options)
}

let fieldsWithOptionsObject ={}
if (fieldsWithOptions) {
  fieldsWithOptions.map((field) => (
    fieldsWithOptionsObject[field.label] = field.options
  ))
}

let incorrectData = []
let dataSelector = []

if (importedUsersWithLabel) {
  importedUsersWithLabel.forEach(user => {
    console.log(user)
     const optionFields = Object.keys(user).filter(key => key in fieldsWithOptionsObject)
     optionFields.forEach(field => {
       console.log(field)

       const correctDataField = fieldsWithOptionsObject[field].find(data => data.name === user[field])


      if (!correctDataField) {
        if (dataSelector.indexOf(field) === -1) {
          const selectorAndMessage = {}
          console.log(arrayHeaderForPlaceHolder)

          selectorAndMessage["errorMessage"] = trad.caution_field + field + trad.not_valid + user.Email + "'."
          selectorAndMessage["selector"] = field
          selectorAndMessage["user"] = user.Email
          selectorAndMessage["wrongData"] = user[field]
          incorrectData.push(selectorAndMessage)
        }
      }
     })
    })
}

// method to send to the state the index of row , of the wrong data and the data to replace 

const updateImportedUsers = (mail,wrongData,event,wrongDataIndex) => {
  if (rows) {
        rows.forEach(row => {
          console.log(row)
      if (row.includes(mail)){
        const updatedData = {}
        updatedData["indexRow"] = rows.indexOf(row)
        // updatedData["indexWrongData"] = row.indexOf(wrongData)
        updatedData["indexWrongData"] = wrongDataIndex
        updatedData["dataToReplace"] = event
        dispatch(updateImportTable(updatedData));
      }
    })  
  }
}




// method to detect a line breack in the array
let lineBreack = false
if (rows) {
  const arraySize = rows.length - 1
  const test = Object.values(rows)

  if (test[arraySize]) {
    if (test[arraySize].length < 2) {
      if (test[arraySize][0].length === 0) {
        lineBreack = true
        enrollFieldArray.data.pop()
      }
    }
  }
}
  

//to display table when enter key is pressed
const handleKeyDown = (event) => {
  if (event.key === 'Enter') {
    // event.preventDefault();  // prevent line breack when enter key is pressed
    const str = enrollFieldValue;
    const results = readString(str);
    dispatch(formatEnrollFieldInArray(results));
    dispatch(isTableDisplayed());
  }
}

  if (isLoading) {
    return <Spinner />;
  } else if (!globalMode) {
    return (
      <Fragment>
        {/* <Typography >{trad.input_field_information}</Typography> */}
        <Typography >{trad.input_field_information_part_one}</Typography>
        <Typography >{trad.input_field_information_part_two}</Typography>
        <Typography >{trad.input_field_information_part_three}</Typography>
        <Typography >{fieldsForPlaceHolder}</Typography>
        <Typography >{trad.input_field_information_part_five}</Typography>
        <TextField
        className={classes.input}
          id="filled-full-width"
          label={trad.label}
          style={{ margin: 8 }}
          placeholder={fieldsForPlaceHolder}
          fullWidth
          value={enrollFieldValue}
          multiline
          rowsMax={10}
          margin="normal"
          InputLabelProps={{
            shrink: true,
          }}
          onChange={(event) => {
            onInputChange(event.target.value);
          }}
          onKeyDown={handleKeyDown}

        />
        <Grid container direction="row" alignItems="center">
        <Grid
          container
          direction="row"
          justify="space-between"
          alignItems="baseline"
        >
            <CheckDataButton/>
            {/* <ImportCsvButton/> */}
            {/* {courses[selectedCourse] &&
              Object.values(courses[selectedCourse].cohorts).length > 0 && (
                <CohortSelector
                  courses={courses}
                  selectedEnrollments={selectedEnrollments}
                  selectedCourseId={selectedCourse}
                />
              )} */}
          </Grid>
          {isTableOpened && <EnrollmentTable />}
        </Grid>
        <Grid>
        {dataMissing &&
            <Typography className={classes.dataMissing}>{trad.data_is_missing}</Typography>
        }
        {lineBreack &&
            <Typography className={classes.dataMissing}>{trad.last_line_is_empty}</Typography>
        }
        {emailError.length > 0 && !lineBreack &&
          emailError.map((email, index) => (

            <Typography key={"emailError" + index} className={classes.wrongEmail}>{email} {trad.wrong_mail}</Typography>

            ))
        }
        {incorrectData.length > 0 &&
          incorrectData.map((object, index) => (
          <Fragment key={index}>
            <Grid className={classes.selectorContainer}>
              <Typography key={"incorrectData" + index} className={classes.wrongEmail}>{object.errorMessage}</Typography>
              <FormControl key={"dataSelector" + index} className={classes.formControl}>
                <InputLabel key={"dataSelectorInput" + index} htmlFor="age-native-simple">{object.selector}</InputLabel>
                  <Select
                    key={"dataSelectorInputIndex" + index}

                      native
                      inputProps={{
                        name: 'label',
                        id: 'label-native-simple',
                      }}
                      onChange={(event) => updateImportedUsers(object.user, object.wrongData, event.target.value, arrayHeaderForPlaceHolder.indexOf(object.selector) )}
                    >
                    {/* <option key={"option" + index} index={"option-"+object.selector} aria-label="None" value="" />
                    {fieldsWithOptionsObject[object.selector].map(data=> (
                      <option key={object.selector+"-"+data.value} index={object.selector+"-"+data.value} value={data.name}>{data.name}</option> */}
                      <option key={"option" + index} index={"option-"} aria-label="None" value="" />
                      {fieldsWithOptionsObject[object.selector].map(data=> (
                        <option key={"-"+data.value} index={"-"+data.value} value={data.name}>{data.name}</option>
                    ))}
                  </Select>
              </FormControl>
            </Grid>
          </Fragment>
            ))
        }

        </Grid>
        {importUsersSuccess &&
          <Grid>
            <Typography className={classes.importSuccess}>{trad.request_send}</Typography>
            <Typography className={classes.importSuccess}>{trad.users_on_the_way_to_be_enrolled}</Typography>
            <Typography className={classes.importSuccess}>{trad.email_will_be_sent_soon}</Typography>
          </Grid>
        }
        <Grid container direction="row" justify="center">

          {enrollFieldArray.data && enrollFieldArray.data.length > 0 && !dataMissing && emailError.length === 0 && incorrectData.length === 0 ? (
            <Button
              variant="contained"
              color="primary"
              component="span"
              className={classes.button}
              onClick={importUsers}
              >
              {trad.import}
            </Button>
          ) : (
            <Button
              disabled
              variant="contained"
              color="primary"
              component="span"
              className={classes.button}
            >
              {trad.import}
            </Button>
          )}

          <label htmlFor="contained-button-file"></label>
        </Grid>
      </Fragment>
    );
  } else {
    return (
      <Fragment>
      </Fragment>
    );
  }
};

export default EnrollUnenroll;