import React, { Fragment} from "react";
import { useDispatch, useSelector } from "react-redux";
import { makeStyles } from "@material-ui/core/styles";

import {
  ListItem,
  ListItemIcon,
  ListItemText,
  Divider,
  Typography,
  IconButton

} from "@material-ui/core";

import AccessTimeIcon from '@material-ui/icons/AccessTime';
import BorderColorIcon from '@material-ui/icons/BorderColor';
import TimeTrackingModal from "./TimeTrackingModal";
import {openTimeTrackingModal} from "../../../store/actions";


const useStyles = makeStyles(() => ({
  icon: {
    marginLeft: "0.5em",
  },
}));

const TimeTracking = () => {
  const classes = useStyles();
  const dispatch = useDispatch();

  const { userProfile } = useSelector((state) => ({
    userProfile: state.quickSearch.userProfile,
  }));

  const timeTrackingArray = Object.values(userProfile.enrollments).filter(course => (
      course.time_tracking !== undefined
  ))

  const openTimeTrackingEditor = (courseId) => {
    dispatch(openTimeTrackingModal(courseId))
  };


timeTrackingArray.forEach(course =>{
  const timeInSecond = course.time_tracking
  if (typeof(timeInSecond) === "number") {
    course.time_tracking = new Date(timeInSecond * 1000).toISOString().substr(11, 8)
  }
})




  return (
    <Fragment>
        {timeTrackingArray.map((course, index) => 
        <Fragment key={index}>
            <ListItem>
                <ListItemIcon >
                  <AccessTimeIcon  />
                </ListItemIcon >
                <ListItemText primary={course.course_name} />
                <Typography>{course.time_tracking}</Typography>

                <IconButton
                  color="primary"
                  onClick={() => openTimeTrackingEditor(course.id)}
                  className={classes.icon}
                  aria-label="add"
                >
                  <BorderColorIcon />
                </IconButton>

              </ListItem>
            <Divider />
        </Fragment>
        
        )
        }
      <TimeTrackingModal/>
    </Fragment>

  );
};

export default TimeTracking;