import React, { Fragment, useCallback } from "react";
import { useSelector } from "react-redux";
import CoursesListItem from "./CoursesListItem";
import Spinner from "../../navigation/Spinner";
import IconButton from "@material-ui/core/IconButton";
import ArrowBackIosIcon from "@material-ui/icons/ArrowBackIos";

const CoursesList = ({ courses, isOpened, handleClick }) => {
  const { isLoading } = useSelector((state) => ({
    isLoading: state.userManagement.isLoading,
  }));

  const renderCoursesList = useCallback(() => {
    const sortedList = [];
    Object.keys(courses).forEach((course) => {
      sortedList.push(course);
    });

    // sortedList.sort();


    return sortedList.map((course) => {
      return (
        <CoursesListItem
          key={course}
          course={[course, courses[course]]}
          globalMode={false}
        />
      );
    });
  }, [courses]);

  const renderGlobalItem = useCallback(() => {
    return <CoursesListItem globalMode={true} />;
  }, []);

  return (
    <Fragment>
      <IconButton aria-label="hidden" onClick={handleClick}>
        <ArrowBackIosIcon />
      </IconButton>
      {renderGlobalItem()}
      {isLoading ? <Spinner /> : renderCoursesList()}
    </Fragment>
  );
};

export default CoursesList;
