import React from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  Grid,
  Input,
  Typography,
  Grow,
  CircularProgress,
  Card,
} from "@material-ui/core";
import SearchIcon from "@material-ui/icons/Search";
import Button from "@material-ui/core/Button";
import { makeStyles } from "@material-ui/core/styles";
import UserTable from "../components/quick-search/UserTable";
import ResetPassword from "../components/quick-search/ResetPassword";
import UnlockAccount from "../components/quick-search/UnlockAccount";
import CoursesProgress from "../components/quick-search/CoursesProgress";
import CustomFieldEditor from "../components/quick-search/CustomFieldEditor";
import { fetchUserProfile, setQuickSearchFieldValue } from "../store/actions";

const useStyles = makeStyles((theme) => ({
  margin: {
    margin: theme.spacing(1),
  },
  spinner: {
    textAlign: "center",
  },
  button: {
    marginLeft: 10,
    width: 150,
  },
  form: {
    marginTop: 20,
    marginBottom: 20,
    display: "flex",
    flexDirection: "column",
  },
  input: {
    paddingLeft: 5,
    height: 36,
    marginRight: 30,
    width: 400,
  },
  searchCard:{
    width:"70%",
    margin: "auto",
    marginTop: "20px",
    marginBottom: "20px"
  }
}));

const QuickSearch = () => {
  // Redux store setup
  const { userProfile, loadingProfile, searchMade, trad, userMail } = useSelector(
    (state) => ({
      userProfile: state.quickSearch.userProfile,
      loadingProfile: state.quickSearch.loadingProfile,
      searchMade: state.quickSearch.searchMade,
      trad: state.translations.values,
      userMail: state.quickSearch.userMail
    })
  );

  const dispatch = useDispatch();

  const setUserEmail = (inputValue) => {
    dispatch(setQuickSearchFieldValue(inputValue));
  };

  const fetchUser = () => {
    dispatch(fetchUserProfile(userMail));
  }

  const handleKeyDown = (event) => {
    if (event.key === 'Enter') {
      dispatch(fetchUserProfile(userMail));
    }
  }

  const classes = useStyles();

  return (
    <div style={{ padding: "20px", margin: "auto" }}>
      <Card

        className={classes.searchCard}
      >
        <Grid
          item
          xs={12}
          lg={8}
          container
          direction="column"
          style={{
            fontWeight: 700,
            backgroundColor: "white",
            padding: 20,
          }}
        >
          <Grid>{trad.click_here_for_profile}</Grid>
          <Grid container spacing={1} alignItems="flex-end">

            <Grid className={classes.form}>
              <Grid container>
                <Grid>
                    <Input
                    value={userMail}
                    label="email"
                    className={classes.input}
                    placeholder={trad.enter_mail}
                    inputProps={{ spellCheck: false }}
                    onChange={(event) => setUserEmail(event.target.value)}
                    onKeyDown={handleKeyDown}
                  />
                </Grid>
                <Grid>
                  <Button
                    className={classes.button}
                    onClick={fetchUser}
                    type="submit"
                    disabled={loadingProfile}
                    variant="contained"
                    color="primary"
                    startIcon={<SearchIcon />}
                  >
                    {trad.launch_search}
                  </Button>
                </Grid>
              </Grid>
              </Grid>
          </Grid>
        </Grid>
      </Card>

      {Object.keys(userProfile).length > 0 && searchMade && (
        <>
          <Grid
            container
            justify="center"
            alignItems="flex-start"
            style={{ marginBottom: "20px" }}
          >
            <Grid item xs={12} lg={8}>
              <UserTable />
            </Grid>
            <Grid item xs={12} lg={4}>
              <ResetPassword />
              {userProfile.is_locked &&
              <UnlockAccount />
              }
            </Grid>
          </Grid>
          <Grid container justify="center" alignItems="flex-start" spacing={6}>
            <Grow in={true}>
              <Grid item xs={12} lg={6}>
                <CoursesProgress />
              </Grid>
            </Grow>
            {(true || userProfile.custom_field_editor_unlocked) && (
              <Grow in={true}>
                <Grid item xs={12} lg={6}>
                  <CustomFieldEditor />
                </Grid>
              </Grow>
            )}
          </Grid>
        </>
      )}
      {Object.keys(userProfile).length === 0 && searchMade && (
        <Typography component="p" style={{ color: "red", margin: "50px" }}>
          {trad.email_doesnt_match}
        </Typography>
      )}
      {loadingProfile && (
        <div className={classes.spinner}>
          <CircularProgress thickness={4} size={120} />
        </div>
      )}
    </div>
  );
};

export default QuickSearch;
