import update from 'immutability-helper';

import {
  FETCH_USER_ENROLLMENTS,
  FILTER_USERS_ENROLLMENTS_BY_COURSE,
  SET_DISPLAY_MODE,
  SET_LOADER,
  FILTER_USERS_BY_COHORT,
  UPDATE_STUDENT_ENROLLMENT,
  CHANGE_PAGE,
  UPDATE_STUDENT_COHORT,
  GET_SEARCHBAR_VALUE,
  SET_TABLE_TO_EMPTY,
  IS_TABLE_DISPLAYED,
  GET_ENROLL_FIELD_VALUE,
  FORMAT_ENROLL_FIELD_VALUE,
  GET_REGISTER_FIELDS_VALUE,
  IMPORT_USERS_FROM_CSV,
  SET_COHORT_FOR_USERS_IMPORT,
  SET_EMAIL_CONTENT,
  DELETE_EMAIL_CONTENT,
  FETCH_EMAILS_HISTORY,
  FETCH_TASKS_HISTORY,
  FETCH_COURSES_LIST,
  FETCH_COURSE_ENROLLMENTS,
  GET_COURSE_ENROLLMENTS_COUNT,
  SET_TAB_INDEX,
  GET_DATA_FROM_UPLOADED_CSV,
  ENABLE_MANUAL_MODE,
  ENABLE_UPLOAD_MODE,
  CHANGE_MODE_FOR_GRADE_REPORT,
  GENERATE_GRADE_REPORT,
  OPEN_MODAL,
  CLOSE_MODAL,
  SET_SCOPE_STATUS,
  UPDATE_IMPORT_TABLE
} from "../actions/types";

const INITIAL_STATE = {
  enrollments: {},
  // isSmallApp: false,
  courses: {},
  selectedEnrollments: {},
  displayGlobalMode: true,
  isLoading: true,
  selectedCohorts: {},
  selectedCourse: "",
  loadedCourses: [],
  cohort: {},
  userDataId: 0,
  page: 0,
  tab: 0,
  searchValue: "",
  searchBarResult: {},
  activeSuggestion: 0,
  showSuggestions: true,
  isTableOpened: false,
  enrollFieldValue: "",
  enrollFieldArray: [],
  registerFieldsValue: [],
  importUsersSuccess: false,
  selectedCohortForImport: {},
  emailContent: {
    cohortsRecipients: [],
    recipients: [],
    subject: "",
    message: "",
  },
  emailsHistory: [],
  tasksHistory: [],
  historySuccess: true,
  gradeReportWithAdmins: [],
  internBoxChecked: true,
  messageModalOpen: false,
  scope: {
    intern:true,
    admin:false
  }
};

const userManagementReducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {
    // GET MICROSITE ENROLLMENTS COUNT TO DETERMINE APP SIZE AND BEHAVIOUR
    case GET_COURSE_ENROLLMENTS_COUNT:
      return {
        ...state,
        isSmallApp: action.payload,
      };

    // FETCH COURSES DETAILS FOR LARGE APPS
    case FETCH_COURSES_LIST:
      return {
        ...state,
        searchValue: "",
        courses: {
          ...state.courses,
          ...action.payload.org_courses,
        },
        isLoading: false,
      };

    // FETCH ALL USERS PROFILES + ENROLLMENTS FOR LARGE APPS
    case FETCH_COURSE_ENROLLMENTS:
      return {
        ...state,
        searchValue: "",
        enrollments: { ...action.payload.enrollments },
        selectedEnrollments: { ...action.payload.selectedEnrollments },
        loadedCourses: [...state.loadedCourses, action.payload.courseId],
        isLoading: false,
        cohort: {},
      };

    // FETCH ALL USERS PROFILES + ENROLLMENTS, AND COURSE DETAILS FOR SMALL APPS
    case FETCH_USER_ENROLLMENTS:
      return {
        ...state,
        searchValue: "",
        enrollments: {
          ...state.enrollments,
          ...action.payload.user_enrollments_profiles,
        },
        courses: {
          ...state.courses,
          ...action.payload.org_courses,
        },
        isLoading: false,
        cohort: {},
      };

    // WHEN SELECTING A COURSE ON LEFT NAV, FOR SMALL APPS
    case FILTER_USERS_ENROLLMENTS_BY_COURSE:
      return {
        ...state,
        searchValue: "",
        selectedEnrollments: {
          ...action.payload.enrollments,
        },
        selectedCourse: action.payload.courseId,
        displayGlobalMode: false,
        cohort: {},
        page: 0,
        tab: 0,
        showSuggestions: true,
        isTableOpened: false,
        enrollFieldValue: "",
        importUsersSuccess: false,
        emailsHistory: [],
        tasksHistory: [],
      };

    // DETERMINE IF GLOBAL MODE OR COURSE MODE
    case SET_DISPLAY_MODE:
      if (action.payload) {
        return {
          ...state,
          searchValue: "",
          displayGlobalMode: action.payload,
          // enrollments: {},
          selectedEnrollments: {},
          cohort: {},
          page: 0,
          tab: 0,
          selectedCourse: "",
          showSuggestions: true,
          isTableOpened: false,
        };
      }
      return {
        ...state,
        displayGlobalMode: action.payload,
        cohort: {},
        page: 0,
      };

    case SET_LOADER:
      return {
        ...state,
        isLoading: action.payload,
      };

    case SET_TAB_INDEX:
      return {
        ...state,
        tab: action.payload,
      };

    case FILTER_USERS_BY_COHORT:
      return {
        ...state,
        selectedCohorts: {
          ...action.payload.filteredCohorts,
        },
        cohort: {
          ...action.payload.cohort,
        },
        page: 0,
      };

    case CHANGE_PAGE:
      return {
        ...state,
        page: action.payload,
      };

    case UPDATE_STUDENT_ENROLLMENT:
      let dataObject = "enrollments";
      if (state.selectedCourse) {
        dataObject = "selectedEnrollments";
        // } else if (state.cohort) {
      } else if (Object.keys(state.cohort).length) {
        dataObject = "selectedCohorts";
      }

      return {
        ...state,
        [dataObject]: {
          ...state[dataObject],
          [action.payload.user.id]: {
            ...state[dataObject][action.payload.user.id],
            enrollments: {
              ...state[dataObject][action.payload.user.id]["enrollments"],
              [action.payload.courseId]: {
                ...state[dataObject][action.payload.user.id]["enrollments"][
                  action.payload.courseId
                ],
                is_active: action.payload.action === "enroll",
              },
            },
          },
        },
      };

    case UPDATE_STUDENT_COHORT:
      let data = "selectedEnrollments";
      if (Object.keys(state.cohort).length) {
        data = "selectedCohorts";
      }
      // TO REFACTO !!!
      return {
        ...state,
        userDataId: action.payload.userDataId,

        [data]: {
          ...state[data],
          [action.payload.userDataId]: {
            ...state[data][action.payload.userDataId],
            enrollments: {
              ...state[data][action.payload.userDataId].enrollments,
              [state.selectedCourse]: {
                ...state[data][action.payload.userDataId].enrollments[
                  state.selectedCourse
                ],
                cohort: {
                  ...action.payload.cohort,
                },
              },
            },
          },
        },
        selectedEnrollments: {
          ...state.selectedEnrollments,
          [action.payload.userDataId]: {
            ...state.selectedEnrollments[action.payload.userDataId],
            enrollments: {
              ...state.selectedEnrollments[action.payload.userDataId]
                .enrollments,
              [state.selectedCourse]: {
                ...state.selectedEnrollments[action.payload.userDataId]
                  .enrollments[state.selectedCourse],
                cohort: {
                  ...action.payload.cohort,
                },
              },
            },
          },
        },
      };

    case GET_SEARCHBAR_VALUE:
      return {
        ...state,
        searchValue: action.payload.searchValue,
        searchBarResult: {
          ...action.payload.searchResult,
        },
        activeSuggestion: action.payload.activeSuggestion,
      };

    case SET_TABLE_TO_EMPTY:
      return {
        ...state,
        showSuggestions: action.payload,
      };

    case IS_TABLE_DISPLAYED:
      return {
        ...state,
        isTableOpened: true,
      };

    case GET_ENROLL_FIELD_VALUE:
      return {
        ...state,
        enrollFieldValue: action.payload.inputValue,
      };

    case FORMAT_ENROLL_FIELD_VALUE:
      return {
        ...state,
        enrollFieldArray: {
          ...action.payload.formatedValue,
        },
      };

    case GET_DATA_FROM_UPLOADED_CSV:
      return {
        ...state,
        enrollFieldArray: {
          ...action.payload
        }
      }

    case GET_REGISTER_FIELDS_VALUE:
      return {
        ...state,
        registerFieldsValue: { ...action.payload },
        importUsersSuccess: false,
      };

    case IMPORT_USERS_FROM_CSV:
      return {
        ...state,
        isTableOpened: false,
        enrollFieldArray: [],
        importUsersSuccess: true,
      };
    case ENABLE_MANUAL_MODE :{
      return {
        ...state,
        importManualMode:true,
        importUploadMode:false,
      }
    }
    case ENABLE_UPLOAD_MODE :{
      return {
        ...state,
        importUploadMode:true,
        importManualMode:false,
      }
    }

    case SET_COHORT_FOR_USERS_IMPORT:
      return {
        ...state,
        selectedCohortForImport: { ...action.payload },
      };

    case SET_EMAIL_CONTENT:
      return {
        ...state,
        emailContent: {
          ...state.emailContent,
          [action.payload.name]: action.payload.value,
        },
      };

    case DELETE_EMAIL_CONTENT:
      return {
        ...state,
        emailContent: action.payload,
      };

    case FETCH_EMAILS_HISTORY:
      return {
        ...state,
        emailsHistory: [...action.payload.data],
        historySuccess: action.payload.historySuccess,
      };

    case FETCH_TASKS_HISTORY:
      return {
        ...state,
        tasksHistory: [...action.payload.data],
        historySuccess: action.payload.historySuccess,
      };

    case CHANGE_MODE_FOR_GRADE_REPORT:
      return {
        ...state,
        internBoxChecked : action.payload
      };

    case GENERATE_GRADE_REPORT:
      return {
        ...state,
        generatedGradeReportMessage: action.payload.sendGradeReportMessage,
      }

    case OPEN_MODAL:
      return {
        ...state,
        messageModalOpen: true,
      }
    case CLOSE_MODAL:
      return {
        ...state,
        messageModalOpen: false,
        generatedGradeReportMessage: "",
        generatedGradeReceiver: ""
      }
    case SET_SCOPE_STATUS:
      return {
        ...state,
        scope:action.payload
      }

    case UPDATE_IMPORT_TABLE:
      return update(state, { 
        enrollFieldArray: { 
            data: {
              [action.payload.indexRow]: {
                [action.payload.indexWrongData] :{
                  $set: action.payload.dataToReplace 
                }
            }
          }
        }
      });
  default:
      return state;
  }
};

export default userManagementReducer;
