import React, { Fragment} from "react";
import { useSelector, useDispatch } from "react-redux";
import { makeStyles } from "@material-ui/core/styles";
import Spinner from "../../navigation/Spinner";
import { Grid } from "@material-ui/core";
import Button from "@material-ui/core/Button";
import TabHeader from "../TabHeader";
import ImportStudents from "./ImportStudents";
import ImportStudentWithCsv from "./ImportStudentWithCsv"

import {
  enableManualMode,
  enableUploadMode
} from "../../../store/actions";


const useStyles = makeStyles((theme) => ({
  button: {
    margin: "20px",
    padding: "6px 40px"
  },

}));

const ImportModeSelector = () => {
  const {
    globalMode,
    isLoading,
    trad,
    importManualMode,
    importUploadMode
  } = useSelector((state) => ({
    globalMode: state.userManagement.displayGlobalMode,
    isLoading: state.userManagement.isLoading,
    selectedCohortForImport: state.userManagement.selectedCohortForImport,
    trad: state.translations.values,
    importManualMode: state.userManagement.importManualMode,
    importUploadMode: state.userManagement.importUploadMode
  }));

  const activateManualMode = () => {
    dispatch(enableManualMode());
  };

  const activateUploadMode = () => {
    dispatch(enableUploadMode());
  };

const dispatch = useDispatch();
const classes = useStyles();

  if (isLoading) {
    return <Spinner />;
  } else if (!globalMode) {
    return (
      <Fragment>
        <TabHeader />
        <Grid container direction="row" justify="center">
            <Button
              variant="contained"
              color="primary"
              component="span"
              className={classes.button}
              onClick={activateManualMode}
              >
              {trad.input_manually}
            </Button>
            <Button
              variant="contained"
              color="primary"
              component="span"
              className={classes.button}
              onClick={activateUploadMode}
              >
              {trad.import_excel_or_csv}
            </Button>
    {importManualMode &&
    <ImportStudents/>}
    {importUploadMode &&
    <ImportStudentWithCsv/>}
            </Grid>
      </Fragment>
    );
  } else {
    return (
      <Fragment>
      </Fragment>
    );
  }
};

export default ImportModeSelector;