import React, { useCallback } from "react";
import { useSelector } from "react-redux";
import { makeStyles, Grid, Button } from "@material-ui/core/";
import MailOutlineIcon from "@material-ui/icons/MailOutline";

const useStyles = makeStyles((theme) => ({
  recipientItem: {
    display: "inline-block",
    textTransform: "capitalize",
  },

  sendButton: {
    width: "150px",
    margin: "10px 8px",
  },
}));

const SendButton = ({ emailContent, toggleModal }) => {
  const { trad } = useSelector((state) => ({
    trad: state.translations.values,
  }));

  const classes = useStyles();
  const { sendButton } = classes;
  const { message, recipients, subject, cohortsRecipients } = emailContent;

  const disableButton = useCallback(() => {
    let hasContent = message.length > 0
    let hasSubject = subject.length > 0
    let hasRecipient = false;
    if (cohortsRecipients.length > 0 || recipients.length > 0){
      hasRecipient = true;
    }
    return !hasContent || !hasSubject || !hasRecipient
  }, [message.length, cohortsRecipients, recipients, subject]);

  return (
    <Grid item xs={2}>
      <Button
        className={sendButton}
        disabled={disableButton()}
        variant="contained"
        color="primary"
        endIcon={<MailOutlineIcon />}
        onClick={() => toggleModal()}
      >
        {trad.send}
      </Button>
    </Grid>
  );
};

export default SendButton;
