import React, { useState, useEffect, useCallback } from "react";
import { useSelector, useDispatch } from "react-redux";
import moment from "moment";
import {  makeStyles } from "@material-ui/core/styles";
import MailOutlineIcon from "@material-ui/icons/MailOutline";
import CancelIcon from "@material-ui/icons/Cancel";
import SubdirectoryArrowRightIcon from "@material-ui/icons/SubdirectoryArrowRight";
import 'moment/locale/fr'  

import {
  Typography,
  Box,
  Tooltip,
} from "@material-ui/core/";
import {
  updateStudentEnrollment,
  fetchCourseEnrollments,
  setLoader,
} from "../../../../store/actions";

const useStyles = makeStyles((theme) => ({
  enrollmentStatus: {
    display: "flex",
    flexDirection: "row",
    alignContent: "center",
    justifyContent: "space-between",
  },
  UnenrollmentStatus: {
    display: "flex",
    flexDirection: "row",
    alignContent: "center",
    justifyContent: "flex-end",
  },
  enrollmentIcons: {
    display: "flex",
    flexDirection: "column",
    alignContent: "center",
    cursor: "pointer",
  },
  enrollmentText: {
    display: "flex",
    flexDirection: "column",
    alignContent: "flex-start",
  }
}));


const EnrollmentStatusGlobal = ({userData, course}) => {
  const classes = useStyles();
  console.log(course)

  // Needed to toggle between red and green enrollment button in prod
  const [enrollmentCheck, setEnrollmentCheck] = useState("initialState")

  const colorUnenroll = "error";
  const colorEnroll = "secondary";

  // Redux store setup
  const {
    enrollments,
    selectedEnrollments,
    globalMode,
    isLoading,
    selectedCourse,
    selectedCohorts,
    cohort,
    searchBarResult,
    searchValue,
    trad,
    loadedCourses,
  } = useSelector((state) => ({
    enrollments: state.userManagement.enrollments,
    selectedEnrollments: state.userManagement.selectedEnrollments,
    globalMode: state.userManagement.displayGlobalMode,
    courses: state.userManagement.courses,
    isLoading: state.userManagement.isLoading,
    selectedCourse: state.userManagement.selectedCourse,
    selectedCohorts: state.userManagement.selectedCohorts,
    cohort: state.userManagement.cohort,
    page: state.userManagement.page,
    searchBarResult: state.userManagement.searchBarResult,
    searchValue: state.userManagement.searchValue,
    showSuggestions: state.userManagement.showSuggestions,
    trad: state.translations.values,
    loadedCourses: state.userManagement.loadedCourses,
  }));

  const [data, setData] = useState(enrollments);

  const dispatch = useDispatch();

  const updateEnrollment = useCallback(
    async (event, action, email, user, courseId) => {
      setEnrollmentCheck(action);
      await dispatch(
        updateStudentEnrollment(event, action, email, user, courseId)
      );
    },
    [dispatch]
  );

  const fetchAllCourseEnrollments = useCallback(
    (selectedCourse) => {
      dispatch(setLoader(true));
      dispatch(fetchCourseEnrollments(selectedCourse));
    },
    [dispatch]
  );

  useEffect(() => {
    if (!globalMode) {
      if (!loadedCourses.includes(selectedCourse)) {
        fetchAllCourseEnrollments(selectedCourse);
      }
    }
  }, [selectedCourse, fetchAllCourseEnrollments, globalMode, loadedCourses]);

  useEffect(() => {
    if (!isLoading && !globalMode) {
      if (Object.keys(searchBarResult).length && searchValue.length > 2) {
        setData(searchBarResult);
      } else if (Object.keys(cohort).length) {
        setData(selectedCohorts);
      } else {
        setData(selectedEnrollments);
      }
    }
  }, [
    selectedEnrollments,
    searchBarResult,
    searchValue,
    enrollments,
    globalMode,
    cohort,
    selectedCohorts,
    isLoading,
  ]);

  return (
    (userData.enrollments.hasOwnProperty(course) &&
    userData.enrollments[course].is_active) && enrollmentCheck !== "unenroll" ? (

      <Box
        component="div"
        className={classes.enrollmentStatus}
      >
        <Box
          component="div"
          className={classes.enrollmentText}>
          <Typography variant="caption">
            {moment(
              userData.enrollments[course].enrollment_date
            ).format("LL")}
          </Typography>
          <Typography variant="caption">
            {userData.enrollments[course].cohort.name}
          </Typography>
        </Box>
        <Box
          component="div"
          className={classes.enrollmentIcons}
        >
          <Tooltip title={trad.unenroll}>
            <CancelIcon
              color={colorUnenroll}
              onClick={(event) =>
                updateEnrollment(
                  event,
                  "unenroll",
                  false,
                  userData,
                  course
                )
              }
            />
          </Tooltip>
          <Tooltip title={trad.notify_unenroll}>
            <MailOutlineIcon
              color={colorUnenroll}
              onClick={(event) =>
                updateEnrollment(
                  event,
                  "unenroll",
                  true,
                  userData,
                  course
                )
              }
            />
          </Tooltip>
        </Box>
      </Box>
    ) : (
      <Box
        component="div"
        className={classes.UnenrollmentStatus}
      >
        <Box
          component="div"
          className={classes.enrollmentIcons}
        >
          <Tooltip title={trad.enroll}>
            <SubdirectoryArrowRightIcon
              color={colorEnroll}
              onClick={(event) =>
                updateEnrollment(
                  event,
                  "enroll",
                  false,
                  userData,
                  course
                )
              }
            />
          </Tooltip>
          <Tooltip title={trad.notify_enroll}>
            <MailOutlineIcon
              color={colorEnroll}
              onClick={(event) =>
                updateEnrollment(
                  event,
                  "enroll",
                  true,
                  userData,
                  course
                )
              }
            />
          </Tooltip>
        </Box>
      </Box>
    )
  );
};

export default EnrollmentStatusGlobal;