import React from "react";
import { useSelector, useDispatch } from "react-redux";
import InputBase from "@material-ui/core/InputBase";
import { makeStyles } from "@material-ui/core/styles";
import SearchIcon from "@material-ui/icons/Search";

import { getSearchBarValue, setTableToEmpty } from "../../store/actions";

const useStyles = makeStyles((theme) => ({
  root: {

    marginLeft: "15px",
  },
  search: {
    position: "relative",
    border: "1px solid black",
    borderRadius: theme.shape.borderRadius,
    backgroundColor: "#fff",
    "&:hover": {
      backgroundColor: "#fffafa",
    },
    marginLeft: 0,
    width:"300px"
  },
  searchIcon: {

    paddingLeft: "10px",
    height: "100%",
    position: "absolute",
    pointerEvents: "none",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    color: "#CCC",
  },
  inputRoot: {
    color: "#000",
  },
  inputInput: {

    paddingLeft: `45px !important`,
    paddingTop: "10px",

    width: "250px",

  },
}));

const SearchBar = ({ data }) => {
  const dispatch = useDispatch();
  const classes = useStyles();

  const onInputChange = (searchText, searchResult, activeSuggestion) => {
    dispatch(getSearchBarValue(searchText, searchResult, activeSuggestion));
  };

  const onInputChangeEmpty = (boleean) => {
    dispatch(setTableToEmpty(boleean));
  };

  const { searchValue, trad} = useSelector(
    (state) => ({
      searchValue: state.userManagement.searchValue,
      trad: state.translations.values,
    })
  );

  const enrollmentsValues = Object.values(data);

  const filteredUsers = enrollmentsValues.filter((users) => {
    return users.email.toLowerCase().includes(searchValue.toLowerCase());
  });

  const convertArrayToObject = (array, key) => {
    const initialValue = {};
    return array.reduce((obj, item) => {
      return {
        ...obj,
        [item[key]]: item,
      };
    }, initialValue);
  };

  const searchResult = convertArrayToObject(filteredUsers, "id");

  const handleKeyDown = (event) => {
    if (event.key === 'Enter') {
      onInputChange(searchValue, searchResult, enrollmentsValues.length)
    }
  }

  const onInputPastedChange = (event) => {
    onInputChange(event.clipboardData.getData('Text'), searchResult, enrollmentsValues.length)
  }

  return (
    <div className={classes.root}>
      <div className={classes.search}>
        <div className={classes.searchIcon}>
          <SearchIcon />
        </div>
        <InputBase
          placeholder={trad.search}
          value={searchValue}
          classes={{
            root: classes.inputRoot,
            input: classes.inputInput,
          }}
          inputProps={{ "aria-label": "search" }}
          onChange={(event) => {
            onInputChange(
              event.target.value,
              searchResult,
              enrollmentsValues.length
            );
            if (!filteredUsers.length) {
              onInputChangeEmpty(false);
            } else if (filteredUsers.length >= 1) {
              onInputChangeEmpty(true);
            }
          }}
          onKeyDown={handleKeyDown}
          onPaste={(event) => {
            onInputPastedChange(event)
            if (!filteredUsers.length) {
              onInputChangeEmpty(false);
            } else if (filteredUsers.length >= 1) {
              onInputChangeEmpty(true);
            }
          }}
        />
      </div>
    </div>
  );
};

export default SearchBar;

