import React, { useCallback, Fragment } from "react";
import { Editor } from "@tinymce/tinymce-react";
import { useDispatch } from "react-redux";

import { makeStyles, Typography } from "@material-ui/core/";

const useStyles = makeStyles((theme) => ({
  editor: {
    padding: "8px 16px",
    fontSize: "12px",
    fontWeight: 700,
    marginBottom: 2,
    color: theme.palette.primary.main,
    textTransform: "uppercase",
  },
}));

const EmailContentEditor = ({ setEmailContent, message }) => {
  const classes = useStyles();
  const { editor } = classes;
  const dispatch = useDispatch();
  const handleEditorChange = useCallback(
    (content) => {
      dispatch(setEmailContent("message", content));
    },
    [dispatch, setEmailContent]
  );

  return (
    <Fragment>
      <Typography variant="h6" component="div" className={editor}>
        Message :
      </Typography>
      <Editor
        value={message}
        initialValue="<p>Edit your content here</p>"
        name="message"
        init={{
          height: 400,
          menubar: false,
          plugins: [
            "advlist autolink lists link image charmap print preview anchor",
            "searchreplace visualblocks code fullscreen",
            "insertdatetime media table paste code help wordcount",
          ],
          toolbar:
            "undo redo | formatselect | bold italic backcolor | alignleft aligncenter alignright alignjustify | bullist numlist outdent indent | removeformat | help",
        }}
        onEditorChange={handleEditorChange}
      />
    </Fragment>
  );
};

export default EmailContentEditor;
