import React, { Fragment, useState } from "react";
import { useSelector } from "react-redux";
import { makeStyles } from "@material-ui/core/styles";
import { Grid, Paper } from "@material-ui/core";
import DehazeIcon from "@material-ui/icons/Dehaze";
import IconButton from "@material-ui/core/IconButton";
import CoursesList from "../components/user-management/side-menu/CoursesList";
import DataRenderer from "../components/user-management/DataRenderer";

const useStyles = makeStyles((theme) => ({
  scrollablePaper: {
    margin: "20px 0px 20px 0px",
    padding: "10px",
    maxHeight: "80vh",
    overflow: "auto",
    direction: "rtl",
  },
  paper: {
    margin: "20px 10px",
  },
  container: {
    width: "95%",
  },
  closedButton: {
    display: "flex",
    flexDirection: "row",
    height: "50px",
  },
  closedIcon: {},
}));

const UserManagement = () => {
  const classes = useStyles();

  const { courses } = useSelector((state) => ({
    courses: state.userManagement.courses,
  }));

  const [isOpened, setOpened] = useState(true);

  const handleClick = () => {
    setOpened(!isOpened);
  };

  return (
    <Grid container>
      {isOpened ? (
        <Fragment>
          <Grid item xs={4}>
            <Paper variant="outlined" className={classes.scrollablePaper}>
              <CoursesList
                courses={courses}
                isOpened={isOpened}
                handleClick={handleClick}
              />
            </Paper>
          </Grid>
        </Fragment>
      ) : (
        <Fragment>
          <IconButton
            aria-label="hidden"
            onClick={handleClick}
            className={classes.closedButton}
          >
            <DehazeIcon className={classes.closedIcon} />
          </IconButton>
        </Fragment>
      )}

      {isOpened ? (
        <Grid item xs={8} className={classes.container}>
          <Paper variant="outlined" className={classes.paper}>
            <DataRenderer />
          </Paper>
        </Grid>
      ) : (
        <Grid className={classes.container}>
          <Paper variant="outlined" className={classes.paper}>
            <DataRenderer />
          </Paper>
        </Grid>
      )}
    </Grid>
  );
};

export default UserManagement;
