import React, { useCallback, useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { makeStyles, lighten } from "@material-ui/core/styles";
import { Divider, ListItem, ListItemText } from "@material-ui/core";
import DescriptionIcon from "@material-ui/icons/Description";
import {
  setDiplayMode,
  filterUsersEnrollmentsByCourse,
} from "../../../store/actions";

const useStyles = makeStyles((theme) => ({
  selectedItem: {
    backgroundColor: lighten(theme.palette.primary.main, 0.86),
    borderRadius: "5px",
    cursor: "pointer",
    margin: "4px 0",
    color: theme.palette.primary.main,
  },
  item: {
    backgroundColor: "white",
    cursor: "pointer",
    margin: "4px 0",
  },
  icon: {
    backgroundColor: theme.palette.primary.main,
    color: "white",
    borderRadius: "10%",
    marginRight: 20,
    padding: 5,
    width: 30,
    height: 30,
  },
  selectedIcon: {
    backgroundColor: lighten(theme.palette.primary.main, 0.86),
    color: theme.palette.primary.main,
    borderRadius: "10%",
    marginRight: 20,
    padding: 0,
    width: 30,
    height: 30,
  },
  primaryText: {
    fontWeight: 900,
    direction:"ltr"
  },
  secondaryText: {
    direction:"ltr"
  },
}));

const CoursesListItem = ({ course, globalMode }) => {
  const classes = useStyles();
  const { enrollments, selectedCourse } = useSelector((state) => ({
    enrollments: state.userManagement.enrollments,
    selectedCourse: state.userManagement.selectedCourse,
  }));

  const [isSelected, setSelection] = useState(false);

  useEffect(() => {
    if (
      (course && selectedCourse === course[0]) ||
      (globalMode && !selectedCourse)
    ) {
      setSelection(true);
    } else {
      setSelection(false);
    }
  }, [course, globalMode, selectedCourse]);
  const dispatch = useDispatch();

  const isGlobal = useCallback(() => {
    dispatch(setDiplayMode(globalMode));
  }, [dispatch, globalMode]);

  const filterEnrollments = useCallback(() => {
    dispatch(filterUsersEnrollmentsByCourse(enrollments, course[0]));
  }, [course, dispatch, enrollments]);

  // specific CMA : add module number on the course_id line
  // let moduleName = ""
  // if (course) {
  //   if (course[0].includes("Module")) {
  //     moduleName = "Module "
  //     if (course[0].slice(-1) === "-") {
  //       moduleName += course[0].slice((course[0].length - 3), (course[0].length - 1)) + " : "
  //     } else {
  //       moduleName += course[0].slice(course[0].length - 2) + " : "
  //     }
  //   }
  // }

  return (
    <div onClick={() => (!globalMode ? filterEnrollments() : isGlobal())}>
      <ListItem className={isSelected ? classes.selectedItem : classes.item}>
        <ListItemText
          classes={{ primary: classes.primaryText, secondary: classes.secondaryText }}
          primary={globalMode ? "GLOBAL" : course[1].display_name}
          // secondary={!globalMode && moduleName + course[0]}
          secondary={!globalMode && course[0]}
        />
        <DescriptionIcon
          className={isSelected ? classes.selectedIcon : classes.icon}
        />
      </ListItem>
      <Divider />
    </div>
  );
};

export default CoursesListItem;
