import React, { Fragment } from "react";
import { useSelector } from "react-redux";
import { makeStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";


const useStyles = makeStyles({
  table: {
    minWidth: 650,
  },
  requireTrue: {
    color: "red",
    fontWeight: "bold"
  },
  requireFalse:{
    color: "black",
    fontWeight: "bold"
  }
});


const EnrollmentTable = () => {

  const { enrollFieldArray, registerFieldsValue} = useSelector((state) => ({
    enrollFieldArray: state.userManagement.enrollFieldArray,
    registerFieldsValue: state.userManagement.registerFieldsValue,
  }));

  const classes = useStyles();

  let rows = enrollFieldArray.data;

  // case when some field need to be displayed in the custom field editor but not in the import section
  const registerFieldsValueArray = Object.values(registerFieldsValue.register_fields);
  const undisplayedFieldsArray = registerFieldsValueArray.filter((field) => !field.undisplay)

  return (
    <TableContainer component={Paper}>
    <Table className={classes.table} aria-label="simple table">
      <TableHead>
        <TableRow>
          {undisplayedFieldsArray.map((field, index) => (
            <Fragment key={index}>
              {field.required === true ? (
                <TableCell key={index} className={classes.requireTrue}>
                  {" "}
                  {field.label}*{" "}
                </TableCell>
              ) : (
                <TableCell key={index} className={classes.requireFalse}> {field.name} </TableCell>
              )}
            </Fragment>
          ))}
        </TableRow>
      </TableHead>
      <TableBody>
        {rows.map((row, index) => (
          <TableRow key={index}>
            {/* {console.log({row, index})} */}
            {row.map((cell, index) => (
              <TableCell key={index} component="th" scope="row">
                {cell}
                {/* {console.log({cell, index})} */}
              </TableCell>
            ))}
          </TableRow>
        ))}
      </TableBody>
    </Table>
  </TableContainer>

  );
};
export default EnrollmentTable;

