import React, { useCallback, Fragment } from "react";
import { useSelector, useDispatch } from "react-redux";
import { ThemeProvider, makeStyles } from "@material-ui/core/styles";
import { Grid } from "@material-ui/core";
import { theme } from "./assets/theme";
import TabPanel from "./components/navigation/TabPanel";
import QuickSearch from "./containers/QuickSearch";
import UserManagement from "./containers/UserManagement";
import CustomTabs from "./components/navigation/CustomTabs";
import CssBaseline from "@material-ui/core/CssBaseline";
import { setNewNavigationValue} from "./store/actions";
import OpenedXConverter from "./containers/OpenedXConverter";


const useStyles = makeStyles(() => ({
  root: {
    backgroundColor: theme.palette.background.main,
    fontFamily: theme.text.font.main,
    paddingTop: 20,
    paddingLeft: 20,
    minHeight: "calc(100vh - 20px)",
    "& .MuiTab-wrapper": {
      fontWeight: 700,
    },
  },
  tabs: {
    width: "calc(100% - 10px)",
  },

}));

const App = () => {

  const dispatch = useDispatch();

  const { trad, value } = useSelector((state) => ({
    trad: state.translations.values,
    value:state.navigation.navValue
  }));

  const classes = useStyles();

  const handleChangeTab = useCallback((event, index) => {
    dispatch(setNewNavigationValue(index));
  }, [dispatch]);

  const renderPanel = useCallback(() => {
    return (
      <Fragment>
        <TabPanel value={value} index={0}>
          <QuickSearch />
        </TabPanel>
        <TabPanel value={value} index={1}>
          <UserManagement />
        </TabPanel>
        <TabPanel value={value} index={2}>
          <OpenedXConverter />
        </TabPanel>
      </Fragment>
    );
  }, [value]);

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Grid className={classes.root}>
        <Grid container className={classes.tabs} >
            <CustomTabs
              value={value}
              handleChangeTab={handleChangeTab}
              tabsList={[ trad.quick_search, trad.user_management, trad.converter ]}
            />
        </Grid>
        {renderPanel()}
      </Grid>
    </ThemeProvider>
  );
};

export default App;
