import React, { useCallback } from "react";
import { makeStyles } from "@material-ui/core/styles";
import { useSelector, useDispatch } from "react-redux";
import {
  IconButton,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Divider,
} from "@material-ui/core";
import ListIcon from "@material-ui/icons/List";
import BorderColorIcon from "@material-ui/icons/BorderColor";
import CheckBoxIcon from "@material-ui/icons/CheckBox";
import TextFieldsIcon from "@material-ui/icons/TextFields";
import EditorCheckbox from "./EditorCheckbox";
import EditorSelector from "./EditorSelector";
import EditorTextField from "./EditorTextField";
import { updateCustomFields } from "../../../store/actions/";

const useStyles = makeStyles(() => ({
  inputField: {
    width: 250,
  },
}));

const CustomFieldItem = ({ config, field }) => {
  const classes = useStyles();
  const { userProfile } = useSelector((state) => ({
    userProfile: state.quickSearch.userProfile,
  }));

  const { name, label, type, options } = field;

  const fields = userProfile[config];


  const dispatch = useDispatch();

  const handleEmptyFieldCreate = useCallback(() => {
    let newValue = "";
    if (type === "select") {
      newValue = options[0].value;
    }
    dispatch(updateCustomFields(name, config, newValue));
  }, [dispatch, config, options, name, type]);

  const handleFieldChange = useCallback(
    (value) => {
      dispatch(updateCustomFields(name, config, value));
    },
    [dispatch, config, name]
  );

  const renderIcon = useCallback((type) => {
    if (type === "checkbox") {
      return (
        <ListItemIcon>
          <CheckBoxIcon />
        </ListItemIcon>
      );
    } else if (type === "select") {
      return (
        <ListItemIcon>
          <ListIcon />
        </ListItemIcon>
      );
    } else if (type === "text") {
      return (
        <ListItemIcon>
          <TextFieldsIcon />
        </ListItemIcon>
      );
    }
  }, []);

  const renderField = useCallback(
    (type, value) => {
      if (type === "checkbox") {
        return (
          <EditorCheckbox
            value={value}
            className={classes.inputField}
            handleFieldChange={handleFieldChange}
          />
        );
      } else if (type === "select") {
        return (
          <EditorSelector
            value={value}
            className={classes.inputField}
            handleFieldChange={handleFieldChange}
            options={options}
          />
        );
      } else {
        return (
          <EditorTextField
            value={value}
            className={classes.inputField}
            handleFieldChange={handleFieldChange}
          />
        );
      }
    },
    [classes.inputField, handleFieldChange, options]
  );

  const renderLabel = useCallback((label) => {
    return <ListItemText primary={label} />;
  }, []);

  if (fields.hasOwnProperty(name)) {
    return (
      <>
        <ListItem>
          {renderIcon(type)}
          {renderLabel(label)}
          {renderField(type, fields[name])}
        </ListItem>
        <Divider />
      </>
    );
  } else {
    return (
      <>
        <ListItem>
          {renderIcon(type)}
          <ListItemText primary={label} />
          <List component="div" disablePadding>
            <IconButton
              aria-label="add"
              color="primary"
              onClick={() => handleEmptyFieldCreate()}
            >
              <BorderColorIcon />
            </IconButton>
          </List>
        </ListItem>
        <Divider />
      </>
    );
  }
};

export default CustomFieldItem;
