import React, { Fragment } from "react";
import { useSelector } from "react-redux";
import DataTable from "./view-enrollments/DataTable";
import DataTableBulk from "./view-enrollments/DataTableBulk";

const EnrollmentsView = () => {
  const { isSmallApp } = useSelector((state) => ({
    isSmallApp: state.userManagement.isSmallApp,
  }));

  return <Fragment>{isSmallApp ? <DataTable /> : <DataTableBulk />}</Fragment>;

};

export default EnrollmentsView;
