import React from "react";
import Paper from "@material-ui/core/Paper";
import { useDispatch, useSelector } from "react-redux";
import { makeStyles } from "@material-ui/core/styles";
import { Grid } from "@material-ui/core";
import Typography from "@material-ui/core/Typography";
import SettingsIcon from "@material-ui/icons/Settings";
import RenderRegistrationStatus from "./mainCourseRender/RenderRegistrationStatus";
import RenderCourseStatus from "./mainCourseRender/RenderCourseStatus";
import SystemUpdateAltIcon from "@material-ui/icons/SystemUpdateAlt";
import Link from "@material-ui/core/Link";
import Checkbox from '@material-ui/core/Checkbox';
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import MessageModal from '../../navigation/MessageModal'

import { generateGradeReport, openModal, setScopeStatus} from "../../../store/actions";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    overflow: "hidden",
    padding: theme.spacing(0, 3),
  },
  paper: {
    margin: `${theme.spacing(1)}px auto`,
    padding: theme.spacing(2),
  },
  paperDate: {
    margin: `${theme.spacing(2)}px auto`,
    padding: theme.spacing(2),

    height: "60px",
  },
  link: {
    color: "inherit",
    cursor: "pointer",
    "&:hover": {
      color: "inherit !important",
    }
  },
  textHover: {
    "&:hover": {
      textDecoration: "underline"
    }
  },
  legend: {
    fontSize: "0.8em",
  },
  radioContainer:{
    flexDirection:"row",
    marginTop: "1em"
  },
  radioButton: {
    marginTop: "-4px"
  },
  formControlLabel:{
    marginTop: "3px"
  },
  checkboxContainer:{
    marginLeft: "2px"
  }

}));

const MainCourse = ({ courseId, trad, selectedCourse }) => {

  const { configuration, scope} = useSelector((state) => ({
    configuration: state.configuration.values,

    scope: state.userManagement.scope

  }));
  const classes = useStyles();
  const dispatch = useDispatch();

  const courseLink = window.props.endpoints.studioLink + selectedCourse

  //   const handleClickUpload = (courseId) => {
  //     if (!scope.admin && !scope.intern ){
  //       dispatch(openModal())
  //     } else {
  //       dispatch(generateGradeReport(courseId, scope));
  //       dispatch(openModal())
  //     }
  // };

  const handleClickUpload = (courseId) => {
      dispatch(generateGradeReport(courseId, scope));
      dispatch(openModal())

  };

  let displayContributeToTheCourseSection = true

  if (configuration) {
    if (configuration.HIDE_CONTRIBUTE_TO_COURSE){
      displayContributeToTheCourseSection = false
    }
  }

const handleChangeIntern = (event) => {

    scope.intern = event.target.checked
    dispatch(setScopeStatus(scope))
};


const handleChangeAdmin = (event) => {
  scope.admin = event.target.checked
  dispatch(setScopeStatus(scope))
};

  return (
    <Grid className={classes.root}>
      <Paper className={classes.paper}>
        <RenderCourseStatus
          startDate={courseId.start}
          endDate={courseId.end}
          trad={trad}
        />
      </Paper>

      <Paper className={classes.paper}>
        <RenderRegistrationStatus
          startDate={courseId.enrollment_start}
          endDate={courseId.enrollment_end}
          trad={trad}
        />
      </Paper>

    {
    displayContributeToTheCourseSection &&
      <Paper className={classes.paper}>
        <Grid container wrap="nowrap" spacing={2}>
          <Grid item>
            <Link
              onClick={() => window.open(courseLink, "_blank")}
              className={classes.link}
              >
              <SettingsIcon />
            </Link>
          </Grid>

          <Grid item xs>
              <Link
              onClick={() => window.open(courseLink, "_blank")}
              className={classes.link}
              >
              <Typography className={classes.textHover}>{trad.contribute_to_the_course}</Typography>
            </Link>
            <Typography className={classes.legend}>
            {trad.redirection}
            </Typography>
          </Grid>
        </Grid>
      </Paper>
    } 

      <Paper className={classes.paper}>
        <Grid container wrap="nowrap" spacing={2}>
          <Grid item>
            <Link
              onClick={() => handleClickUpload(selectedCourse)}
              className={classes.link}
            >
              <SystemUpdateAltIcon />
            </Link>
          </Grid>
          <Grid item xs>
            <Link
              onClick={() => handleClickUpload(selectedCourse)}
              className={classes.link}
            >
              <Typography className={classes.textHover}>{trad.generate_grade_report}</Typography>
            </Link>
            <Typography className={classes.legend}>
              {trad.send_report}
            </Typography>
          </Grid>
        </Grid>
        {/* <Grid className={classes.checkboxContainer}>
          <FormControl component="fieldset">
            <FormGroup aria-label="position" row>
              <FormControlLabel
                value="intern"
                control={<Checkbox color="primary" onChange={handleChangeIntern} defaultChecked={true} />}
                label={<Typography className={classes.formControlLabel}>{trad.include_student}</Typography>}
                labelPlacement="end"
              />
              <FormControlLabel
                value="admin"
                control={<Checkbox color="primary" onChange={handleChangeAdmin} />}
                label={<Typography className={classes.formControlLabel}>{trad.include_admin}</Typography>}
                labelPlacement="end"
              />
            </FormGroup>
          </FormControl>
        </Grid> */}
      </Paper>
      <MessageModal/>
    </Grid>
  );
};

export default MainCourse;
