import React, { useCallback, useEffect, useState, Fragment } from 'react';
import { useSelector, useDispatch } from "react-redux";
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TablePagination from '@material-ui/core/TablePagination';
import TableRow from '@material-ui/core/TableRow';
import HistoryButton from './HistoryButton';
import CustomSnackbar from '../../quick-search/cfe-components/Snackbar';
import { fetchEmailsHistory, fetchTasksHistory } from "../../../store/actions"


const useStyles = makeStyles((theme) => ({
  root: {
    margin: "25px auto",
    width: '95%',
    borderTop: `1px solid gainsboro`,
    paddingTop: 25
  },
  container: {
    width: "100%"
  },
  headCells: {
    fontWeight: 700,
    maxWidth: "20%",
    backgroundColor: "gainsboro",
    height: "30px !important",
    overflowWrap: "unset"
  },
  historyType: {
    fontWeight: 500,
    fontSize: 20,
  },
  historyBlock: {
    marginBottom: 20,
  }
}));

const HistoryTables = ({ dataSource }) => {
  const classes = useStyles();
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [data, setData] = useState([]);
  const [headerCells, setHeaderCells] = useState([])
  const [openSnack, setOpenSnack] = useState(false);

  const { selectedCourse, emailsHistory, tasksHistory, historySuccess } = useSelector((state) => ({
    selectedCourse: state.userManagement.selectedCourse,
    emailsHistory: state.userManagement.emailsHistory,
    tasksHistory: state.userManagement.tasksHistory,
    historySuccess: state.userManagement.historySuccess
  }));

  const dispatch = useDispatch();

  const getDataHistory = useCallback(() => {
    if(dataSource === "emailsHistory") {
      dispatch(fetchEmailsHistory(selectedCourse))
    } else if(dataSource === "tasksHistory") {
      dispatch(fetchTasksHistory(selectedCourse))
    }
  }, [dispatch, selectedCourse, dataSource])

  useEffect(() => {
    if(dataSource === "emailsHistory") {
      emailsHistory && setData(emailsHistory)
    } else if(dataSource === "tasksHistory") {
      tasksHistory && setData(tasksHistory)
    }
  }, [setData, emailsHistory, tasksHistory, dataSource])

  useEffect(() => {
    !historySuccess && setOpenSnack(true)
  }, [historySuccess])

  useEffect(() => {
    let newCells = []
    if (dataSource === "emailsHistory") {
      newCells = [
        { id: "subject", label: "Subject" },
        { id: "requester", label: "Requester" },
        { id: "sent_to", label: "Recipients" },
        { id: "created", label: "Date" },
        { id: "number_sent", label: "Number" },
      ]
    } else if (dataSource === "tasksHistory") {
      newCells = [
        { id: "task_type", label: "Type" },
        { id: "task_input", label: "Input" },
        { id: "task_id", label: "Id" },
        { id: "requester", label: "Sender" },
        { id: "created", label: "Date" },
        { id: "duration_sec", label: "Dur. (sec)" },
        { id: "task_state", label: "State" },
        { id: "status", label: "Status" },
        { id: "task_message", label: "Progression" },
      ]
    }
    setHeaderCells(newCells)
  }, [dataSource])


  const handleChangePage = useCallback((event, newPage) => {
    setPage(newPage);
  }, []);

  const handleChangeRowsPerPage = useCallback((event) => {
    setRowsPerPage(+event.target.value);
    setPage(0);
  }, []);

  const renderButtonText = useCallback(() => {
    if (data.length) {
      return "Refresh"
    }
    return "Display"
  }, [data])

  return (
    <Grid className={classes.root}>
      <Grid container direction="row" justify="space-between" alignItems="center" className={classes.historyBlock} >
        <Box component="div" className={classes.historyType} >{dataSource === "emailsHistory" ? "Emails history" : "Tasks history"}</Box>
        <HistoryButton getDataHistory={getDataHistory} text={renderButtonText()} />
      </Grid>

      <CustomSnackbar
        open={openSnack}
        setOpenSnack={setOpenSnack}
        snackData={{ message: dataSource === "emailsHistory" ? "No emails have been sent yet" : "No tasks have been executed yet", severity: "warning" }}
      />
      {data.length ?
        <Fragment>
          <TableContainer className={classes.container}>
            <Table>
              <TableHead>
                <TableRow>
                  {headerCells.map((cell) => (
                    <TableCell
                      className={classes.headCells}
                      key={cell.id}
                      size="medium"
                    >
                      {cell.label}
                    </TableCell>
                  ))}
                </TableRow>
              </TableHead>
              <TableBody>
                {data.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((row) => {
                  return (
                    <TableRow hover role="checkbox" tabIndex={-1} key={row.id ? row.id : row.task_id}>
                      {headerCells.map((column, index) => {
                        return (
                          <TableCell key={index}>
                            {row[column.id]}
                          </TableCell>
                        );
                      })}
                    </TableRow>
                  );
                })}
              </TableBody>
            </Table>
          </TableContainer>
          <TablePagination
            rowsPerPageOptions={[10, 25, 100]}
            component="div"
            count={data.length}
            rowsPerPage={rowsPerPage}
            page={page}
            onChangePage={handleChangePage}
            onChangeRowsPerPage={handleChangeRowsPerPage}
          />
        </Fragment> : null
      }
    </Grid>
  );
}

export default HistoryTables;