import React, { Fragment} from "react";
import { useSelector } from "react-redux";
import {
  ListItem,
  ListItemIcon,
  ListItemText,
  Divider,
  Typography,

} from "@material-ui/core";

import CreateIcon from '@material-ui/icons/Create';
import EventNoteIcon from '@material-ui/icons/EventNote';
import moment from "moment";



const LastModificationView = () => {


  const { trad, userProfile } = useSelector((state) => ({
    userProfile: state.quickSearch.userProfile,
    trad: state.translations.values,
  }));

  const formatedDate = moment(userProfile.custom_field.last_update_date).format("DD/MM/YYYY");

  return (
    <Fragment>
          <Fragment >
          <ListItem>
            <ListItemIcon>
                <CreateIcon />
            </ListItemIcon>
            <ListItemText primary={trad.last_modification_maker} />
            <Typography>{userProfile.custom_field.last_update_maker}</Typography>
            </ListItem>
            <Divider />
            <ListItem>
            <ListItemIcon>
                <EventNoteIcon />
            </ListItemIcon>
            <ListItemText primary={trad.last_modification_date} />
            <Typography>{formatedDate}</Typography>
            </ListItem>
          </Fragment>

    </Fragment>
  );
};

export default LastModificationView;