import React, { useCallback } from "react";
import { useSelector, useDispatch } from "react-redux";
import { makeStyles } from "@material-ui/core/styles";
import {
  FormControl,
  InputLabel,
  NativeSelect,
  Input,
} from "@material-ui/core/";

import { filterUsersEnrollmentsByCohort } from "../../store/actions";

const useToolbarStyles = makeStyles((theme) => ({
  formControl: {
    margin: theme.spacing(1),
    minWidth: 200,
    maxWidth: 300,
  },
}));

const RenderCohortList = ({
  courses,
  selectedEnrollments,
  selectedCourseId,
}) => {
  const { selectedCohort, trad } = useSelector((state) => ({
    selectedCohort: state.userManagement.selectedCohort,
    trad: state.translations.values
  }));

  const classes = useToolbarStyles();

  const dispatch = useDispatch();

  const selectCohort = useCallback(
    (selectedCohort) => {
      let cohortObject = {};
      Object.entries(courses[selectedCourseId].cohorts).forEach((cohort) => {
        if (selectedCohort === cohort[1]) {
          cohortObject = {
            id: cohort[0],
            name: selectedCohort,
          };
        }
      });
      dispatch(
        filterUsersEnrollmentsByCohort(
          selectedEnrollments,
          cohortObject,
          selectedCourseId
        )
      );
    },
    [selectedEnrollments, selectedCourseId, dispatch, courses]
  );

  const cohorts = Object.entries(courses[selectedCourseId].cohorts);

  return (
    <FormControl className={classes.formControl}>
      <InputLabel htmlFor="demo-customized-select-native">{trad.cohorts}</InputLabel>
      <NativeSelect
        value={selectedCohort}
        id="demo-customized-select-native"
        input={<Input />}
        onChange={(event) => selectCohort(event.target.value)}
      >
        <option value=""></option>
        {cohorts.map((cohort) => (
          <option key={cohort[0]} value={cohort[1]}>
            {cohort[1]}
          </option>
        ))}
      </NativeSelect>
    </FormControl>
  );
};

export default RenderCohortList;
