import React from "react";
import { useSelector, useDispatch } from "react-redux";
import { CSVReader } from "react-papaparse";
import { makeStyles } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";

import {
  isTableDisplayed,
  // formatEnrollFieldInArray,
  getDataFromUploadedCsv
} from "../../../store/actions";

const useStyles = makeStyles((theme) => ({
  button: {
    margin: "10px",
  }
}));
const buttonRef = React.createRef();

const ImportCsvButton = () => {

  const classes = useStyles();

  const { trad } = useSelector((state) => ({
    trad: state.translations.values,
  }));

  const dispatch = useDispatch();

  // const handleClick = () => {
  //   const str = enrollFieldValue;
  //   const results = readString(str);
  //   dispatch(formatEnrollFieldInArray(results));

  // };

  const displayTable = () => {
    dispatch(isTableDisplayed());
  };


const handleOpenDialog = (e) => {
    // Note that the ref is set async, so it might be null at some point
    if (buttonRef.current) {
      buttonRef.current.open(e);
    }
  };

 const  handleOnFileLoad = (data) => {
    data = data.slice(1)
    console.log('---------------------------');
    console.log(data);
    console.log('---------------------------');
    const enrollFieldArray = {}
    enrollFieldArray["data"] = data.map((element => 
      element.data
    ))
    dispatch(getDataFromUploadedCsv(enrollFieldArray));
    displayTable();
  };

 const  handleOnError = (err, file, inputElem, reason) => {
    console.log('---------------------------');
    console.log(err);
    console.log('---------------------------');
  };

// const   handleOnRemoveFile = (data) => {
//     console.log('---------------------------');
//     console.log(data);
//     console.log('---------------------------');
//   };

//  const  handleRemoveFile = (e) => {
//     // Note that the ref is set async, so it might be null at some point
//     if (buttonRef.current) {
//       buttonRef.current.removeFile(e);
//     }
//   };

  return (
    <>

      <CSVReader
      onFileLoad={handleOnFileLoad}
      onError={handleOnError}
      ref={buttonRef}
      noClick
      noDrag

      >
        {({ file }) => (
        <aside
          style={{
            display: 'flex',
            flexDirection: 'row',
            marginBottom: 10,
            alignItems: "baseline"
          }}
        >
          <Button
          className={classes.button}
          onClick={handleOpenDialog}
          variant="contained"
          color="primary"
          component="span">
            {trad.browse_file}
          </Button>
          <Typography>{file && file.name}</Typography>
          {/* <button
            style={{
              borderRadius: 0,
              marginLeft: 0,
              marginRight: 0,
              paddingLeft: 20,
              paddingRight: 20,
            }}
            onClick={handleRemoveFile}
          >
            Remove
          </button> */}
        </aside>
        )}
      </CSVReader>
    </>
  );
};

export default ImportCsvButton;

