import React, { useState, useCallback } from "react";
import {
  Button,
  CircularProgress,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  Grid,
} from "@material-ui/core";
import { useSelector, useDispatch } from "react-redux";
import { makeStyles } from "@material-ui/core/styles";
import NoEncryptionIcon from "@material-ui/icons/NoEncryption";
import { unlockUserAccount } from "../../store/actions";

const useStyles = makeStyles((theme) => ({
  button: {
    margin: theme.spacing(1),
  },
  dialogBottom: {
    display: "flex",
    alignContent: "center",
    justifyContent: "center",
    width: "100%",
    height: 50,
    marginBottom: 20,
  },
}));

export default function IconLabelButtons() {
  const classes = useStyles();
  const [open, setOpen] = useState(false);
  const [loading, setLoading] = useState(false);


  const { userProfile, email, trad } = useSelector((state) => ({
    userProfile: state.quickSearch.userProfile,
    email: state.quickSearch.userProfile.email,
    trad: state.translations.values,
  }));

  const toggleDialog = useCallback(() => {
    setOpen(!open);
  }, [open]);

  const dispatch = useDispatch();

  const unlockAcount = useCallback(async () => {
    setLoading(true);
    await dispatch(unlockUserAccount(email));
    toggleDialog();
    setLoading(false);
  }, [dispatch, email, toggleDialog]);

  return (
    <>
      <Grid style={{ textAlign: "center" }}>
        <Button
          disabled={userProfile.is_locked}
          variant="contained"
          color="primary"
          className={classes.button}
          startIcon={<NoEncryptionIcon />}
          onClick={toggleDialog}>
          {trad.unlock_account}
        </Button>
      </Grid>
      <Dialog open={open}>
        <DialogTitle>{trad.unlock_account}</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            {trad.unlock_account_question}

          </DialogContentText>
        </DialogContent>
        <DialogActions className={classes.dialogBottom}>
          {!loading ? (
            <>
            <Grid container justify="flex-end">
              <Button onClick={unlockAcount} color="primary">
                {trad.yes}
              </Button>
              <Button onClick={toggleDialog} color="primary" autoFocus>
                {trad.no}
              </Button>
            </Grid>
            </>
          ) : (
            <CircularProgress thickness={2} size={50} />
          )}
        </DialogActions>
      </Dialog>
    </>
  );
}
