import React from "react";
import { useSelector, useDispatch } from "react-redux";
import { readString } from "react-papaparse";
import { makeStyles } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";

import {
  isTableDisplayed,
  formatEnrollFieldInArray,
} from "../../../store/actions";

const useStyles = makeStyles((theme) => ({
  button: {
    marginLeft: "10px",
  }
}));

const CheckDataButton = () => {

  const classes = useStyles();

  const { enrollFieldValue, trad } = useSelector((state) => ({
    enrollFieldValue: state.userManagement.enrollFieldValue,
    trad: state.translations.values,
  }));

  const dispatch = useDispatch();

  const handleClick = () => {
    const str = enrollFieldValue;
    const results = readString(str);
    dispatch(formatEnrollFieldInArray(results));

  };

  const displayTable = () => {
    dispatch(isTableDisplayed());
  };

  return (
      <Button
        className={classes.button}
        variant="contained"
        color="primary"
        onClick={() => {
          handleClick();
          displayTable();
        }}
      >
        {trad.check_data}
      </Button>
  );
};

export default CheckDataButton;
