export const getCourseEnrollmentsCount = () => async (dispatch) => {
  let data = {};

  if (process.env.NODE_ENV !== "development") {
    const url = window.props.endpoints.getCourseEnrollmentsCount
    const settings = {
      method: "GET",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
      },
    };
    const api_call = await fetch(url, settings);
    data = await api_call.json();
  } else {
    data = window.props;
  }

  if (data.count > 5000) {
    dispatch({ type: GET_COURSE_ENROLLMENTS_COUNT, payload: false });
  } else {
    dispatch({ type: GET_COURSE_ENROLLMENTS_COUNT, payload: true });
  }
};

