export const fetchCourseEnrollments = (courseId) => async (
  dispatch,
  getState
) => {
  const { userManagement } = getState();
  let newEnrollments = { ...userManagement.enrollments };
  let newSelectedEnrollments = { ...userManagement.selectedEnrollments };
  let data = {};
  if (process.env.NODE_ENV !== "development") {
    const url = window.props.endpoints.fetchCourseEnrollments.replace("<courseId>", courseId)
    const settings = {
      method: "GET",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
      },
    };
    const api_call = await fetch(url, settings);
    data = await api_call.json();
  } else {
    data = window.props;
  }

  // ADD COURSE ENROLLMENTS TO EXISTING PROFILES OR CREATE PROFILE FOR UNLOADED PROFILES
  Object.entries(data.user_enrollments_profiles).forEach((user) => {
    if (user[0] in newEnrollments) {
      newEnrollments[user[0]].enrollments = {
        ...newEnrollments[user[0]].enrollments,
        ...user[1].enrollments,
      };
    } else {
      newEnrollments[user[0]] = user[1];
    }

    // MAKE USER SELECTION REGARDING COURSE ENROLLMENT
    if (courseId in user[1].enrollments) {
      if (!(user[0] in newSelectedEnrollments)) {
        newSelectedEnrollments[user[0]] = { ...user[1] };
      }

      if (user[0] in newSelectedEnrollments) {
        newSelectedEnrollments[user[0]].enrollments = {
          ...newSelectedEnrollments[user[0]].enrollments,
          ...{ [courseId]: user[1].enrollments[courseId] },
        };
      }
    }
  });

  let payload = {
    courseId,
    enrollments: newEnrollments,
    selectedEnrollments: newSelectedEnrollments,
  };

  dispatch({ type: FETCH_COURSE_ENROLLMENTS, payload: payload });
};

export const filterUsersEnrollmentsByCourse = (users, courseId) => {
  let filteredUsersEnrollments = {};
  if (courseId) {
    Object.entries(users).forEach((user) => {
      if (user[1].enrollments.hasOwnProperty(courseId)) {
        if (!filteredUsersEnrollments.hasOwnProperty(user[0])) {
          filteredUsersEnrollments[user[0]] = { ...user[1] };
          filteredUsersEnrollments[user[0]].enrollments = {};
        }

        if (filteredUsersEnrollments.hasOwnProperty(user[0])) {
          if (user[1].enrollments.hasOwnProperty(courseId)) {
            filteredUsersEnrollments[user[0]].enrollments = {
              ...filteredUsersEnrollments[user[0]].enrollments,
              ...{ [courseId]: user[1].enrollments[courseId] },
            };
          }
        }
      }
    });
  }
  let payload = { enrollments: filteredUsersEnrollments, courseId: courseId };
  return {
    type: FILTER_USERS_ENROLLMENTS_BY_COURSE,
    payload: payload,
  };
};

