export const filterUsersEnrollmentsByCohort = (
  selectedEnrollments,
  cohort,
  courseId
) => {
  let filteredUsersCohorts = {};
  Object.entries(selectedEnrollments).forEach((user_enrollment) => {
    if (user_enrollment[1].enrollments[courseId].cohort.name === cohort.name) {
      filteredUsersCohorts[user_enrollment[0]] = user_enrollment[1];
    }
  });
  let payload = {
    filteredCohorts: { ...filteredUsersCohorts },
    cohort: cohort,
  };
  return { type: FILTER_USERS_BY_COHORT, payload };
};

export const updateStudentEnrollment = (
  event,
  action,
  email_students,
  user,
  courseId
) => async (dispatch) => {
  event.stopPropagation();
  let data = {};
  let payload = {};

  if (process.env.NODE_ENV !== "development") {
    const formcontent = {
      action: action,
      identifiers: user.email,
      email_students: email_students,
      auto_enroll: true,
      role: "Learner",
      reason: "",
    };

    var formBody = [];
    for (var property in formcontent) {
      var encodedKey = encodeURIComponent(property);
      var encodedValue = encodeURIComponent(formcontent[property]);
      formBody.push(encodedKey + "=" + encodedValue);
    }
    formBody = formBody.join("&");
    const url = window.props.endpoints.updateStudentEnrollment.replace("<courseId>", courseId)

    const settings = {
      method: "POST",
      headers: {
        "X-CSRFToken": window.props.csrfToken,
        Accept: "application/json, text/javascript, */*; q=0.01",
        "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8",
        "X-Requested-With": "XMLHttpRequest",
      },
      processData: false,
      body: formBody,
    };

    const api_call = await fetch(url, settings);
    data = await api_call.json();
    const result = data.results[0];
    if (result.before !== result.after) {
      payload = {
        user,
        courseId,
        action,
      };
      dispatch({ type: UPDATE_STUDENT_ENROLLMENT, payload: payload });
    }
  } else {
    data = "local response";
    payload = {
      user,
      courseId,
      action,
    };
    dispatch({ type: UPDATE_STUDENT_ENROLLMENT, payload: payload });
  }
};

