export const updateStudentCohort = (
  event,
  userDataId,
  cohort,
  courseId,
  userEmail
) => async (dispatch) => {
  let data = {};
  let payload = {};
  if (process.env.NODE_ENV !== "development") {
    // let formData = new FormData();
    // formData.append("users", userEmail);

    const url = window.props.endpoints.updateStudentCohort.replace("<courseId>", courseId).replace("<cohort.id>", cohort.id)

    let formcontent = {
      users: JSON.stringify(userEmail),
    };

    var formBody = [];
    for (var property in formcontent) {
      var encodedKey = encodeURIComponent(property);
      var encodedValue = encodeURIComponent(formcontent[property]).replaceAll("%22", "");
      formBody.push(encodedKey + "=" + encodedValue);
    }
    formBody = formBody.join("&");

    const settings = {
      method: "POST",
      headers: {
        "X-CSRFToken": window.props.csrfToken,
        Accept: "application/json, text/javascript, */*; q=0.01",
        "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8",
        "X-Requested-With": "XMLHttpRequest",
      },
      contentType: false,
      processData: false,

      body: formBody

    };
    const api_call = await fetch(url, settings);
    data = await api_call.json();
    const success = data.success;
    if (success) {
      payload = {
        cohort,
        userDataId,
      };
      dispatch({ type: UPDATE_STUDENT_COHORT, payload: payload });
    }
  } else {
    let payload = {
      cohort,
      userDataId,
    };
    dispatch({ type: UPDATE_STUDENT_COHORT, payload });
  }
};

export const fetchUserProfile = (userEmail) => async (dispatch) => {
  let data = {};
  let payload = {};

  dispatch({ type: SET_PROFILE_LOADER, payload: true });

  if (process.env.NODE_ENV !== "development") {
    const url = window.props.endpoints.fetchUserProfile.replace("<userEmail>", userEmail)
    const settings = {
      method: "GET",
      headers: {
        "X-CSRFToken": window.props.csrfToken,
      },
      contentType: false,
      processData: false,
    };
    const api_call = await fetch(url, settings);
    data = await api_call.json();
    payload = data;
    dispatch({ type: FETCH_USER_PROFILE, payload: payload });
  } else {
    // In case of development work, we simulate the call delay with a set timeout
    setTimeout(function () {
      var userList = Object.keys(window.props.user_enrollments_profiles);
      for (let i = 0; i < userList.length; i++) {
        if (
          window.props.user_enrollments_profiles[userList[i]].email ===
          userEmail
        ) {
          payload = window.props.user_enrollments_profiles[userList[i]];
          break;
        }
      }
      dispatch({ type: FETCH_USER_PROFILE, payload: payload });
    }, 1000);
  }
};

