export const sendEmail = (
  coursesArray,
  emailContent,
  courses,
  globalMode
) => async (dispatch) => {
  const { recipients, cohortsRecipients, subject, message } = emailContent;
  let status = 200;
  if (process.env.NODE_ENV !== "development") {
    if (globalMode) {
      await coursesArray.forEach(async (courseId) => {
        let formData = new FormData();
        formData.append("action", "send");
        formData.append("subject", subject);
        formData.append("message", message);
        let formatedCohortRecipients = [];
        cohortsRecipients.forEach((cohort) => {
          if (Object.values(courses[courseId].cohorts).indexOf(cohort !== -1)) {
            formatedCohortRecipients.push(`cohort:${cohort}`);
          }
        });
        formData.append(
          "send_to",
          JSON.stringify(recipients.concat(formatedCohortRecipients))
        );
        const url = window.props.endpoints.sendEmail.replace("<courseId>", courseId)
        const settings = {
          method: "POST",
          headers: {
            "X-CSRFToken": window.props.csrfToken,
          },
          contentType: false,
          processData: false,
          body: formData,
        };
        const api_call = await fetch(url, settings);
        status = api_call.status;
      });
    } else {
      await coursesArray.forEach(async (courseId) => {
        let formatedCohortRecipients = [];
        cohortsRecipients.forEach((cohort) => {
          formatedCohortRecipients.push(`cohort:${cohort}`);
        });
        let formData = new FormData();
        formData.append("action", "send");
        formData.append("subject", subject);
        formData.append("message", message);
        formData.append(
          "send_to",
          JSON.stringify(recipients.concat(formatedCohortRecipients))
        );

        const url = window.props.endpoints.sendEmail.replace("<courseId>", courseId)
        const settings = {
          method: "POST",
          headers: {
            "X-CSRFToken": window.props.csrfToken,
          },
          contentType: false,
          processData: false,
          body: formData,
        };
        const api_call = await fetch(url, settings);
        status = api_call.status;
      });
    }
  }

  let payload = {
    recipients: [],
    cohortsRecipients: [],
    subject: "",
    message: "",
  };
  if (status === 200) {
    dispatch({ type: DELETE_EMAIL_CONTENT, payload });
  }
};

