export const updateUserTimeTracking = (courseId, secondsToAdd, userEmail) => async (dispatch) =>  {
  let payload = {
    courseId,
    secondsToAdd
  }
  if (process.env.NODE_ENV !== "development") {
   let formcontent = {
      user_email: JSON.stringify(userEmail),
      time: JSON.stringify(secondsToAdd),
      course_section: JSON.stringify("extra"),
      course_sub_section: JSON.stringify("extra"),
    };

    var formBody = [];
    for (var property in formcontent) {
      var encodedKey = encodeURIComponent(property);
      var encodedValue = encodeURIComponent(formcontent[property]).replaceAll("%22", "");
      formBody.push(encodedKey + "=" + encodedValue);
    }
    formBody = formBody.join("&");

   let url= window.props.endpoints.updateUserTimeTracking.replace("<courseId>", courseId)

   let settings = {
    method: "POST",
    headers: {
      "X-CSRFToken": window.props.csrfToken,
      Accept: "application/json, text/javascript, */*; q=0.01",
      "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8",
      "X-Requested-With": "XMLHttpRequest",
    },
    body: formBody
    };
   let response = await fetch(url, settings);
    if(response.ok) {
        formcontent = {
        user_email: JSON.stringify(userEmail),
      };
  
      formBody = [];
      for (var element in formcontent) {
         encodedKey = encodeURIComponent(element);
         encodedValue = encodeURIComponent(formcontent[element]).replaceAll("%22", "");
        formBody.push(encodedKey + "=" + encodedValue);
      }

      url = window.props.endpoints.updateUserTimeTrackingSuccess.replace("<courseId>", courseId)
        settings = {
        method: "POST",
        headers: {
          "X-CSRFToken": window.props.csrfToken,
          Accept: "application/json, text/javascript, */*; q=0.01",
          "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8",
          "X-Requested-With": "XMLHttpRequest",
        },
        body: formBody
        };
        response = await fetch(url, settings);
    }
    
  }

  dispatch({ type: UPDATE_USER_TIME_TRACKING, payload });
};

