export const convertToTarfile = (
  degreeOfCert,
  courseTitle,
  excelFile
) => async (dispatch) => {

  let payload = { displaySpinner : true };
  dispatch({type: DISPLAY_SPINNER, payload });

  payload = { displayTarfileLink : true, displayErrorMessage: false };
  let status = 200;

  let formData = new FormData();
  formData.append("excelFile", excelFile);
  formData.append("courseTitle", courseTitle);
  formData.append("degreeOfCert", degreeOfCert);

  const url = window.props.endpoints.convertToTarfile;
  // const url = "/tma_apps/convert_to_tarfile/"
  const settings = {
    method: "POST",
    headers: {
      "X-CSRFToken": window.props.csrfToken,
    },
    contentType: false,
    processData: false,
    body: formData
  };
  const api_call = await fetch(url, settings);
  status = api_call.status;

  let data = "init"
  try {
    data = await api_call.json();
  } catch (error) {
    
  }

  // console.log(data.message) to be displayed on front
  if (data != "init") {
    payload = { displayTarfileLink : false, displayErrorMessage : data.message }
    
  }  

  if (status === 200) {
    dispatch({ type: DISPLAY_TARFILE_LINK, payload });
  } else {
    payload = { displayErrorMessage: "A server related error occured, please try again in a few minutes.", displayTarfileLink : false }
    dispatch({ type: DISPLAY_TARFILE_LINK, payload });
  }; 
};
