import React, { useEffect, useCallback } from "react";
import { useSelector } from "react-redux";
import { makeStyles } from "@material-ui/core/styles";
import CustomFielsList from "./cfe-components/CustomFieldsList.jsx";
import Snackbar from "./cfe-components/Snackbar.jsx";
import PublishIcon from "@material-ui/icons/Publish";
import { CardContent, Card, Button } from "@material-ui/core";
import { saveNewCustomFields } from "../../store/actions";
import { useDispatch } from "react-redux";

const useStyles = makeStyles((theme) => ({
  root: {
    width: "100%",
    backgroundColor: theme.palette.background.paper,
    padding: 20,
  },
  nested: {
    paddingLeft: theme.spacing(4),
  },
  button: {
    width: "100%",
  },
}));

const CustomFieldEditor = () => {
  const { userProfile, trad, snackSettings, customFieldSaveOn } = useSelector(
    (state) => ({
      userProfile: state.quickSearch.userProfile,
      trad: state.translations.values,
      snackSettings: state.quickSearch.snackSettings,
      customFieldSaveOn: state.quickSearch.customFieldSaveOn,
    })
  );

  const classes = useStyles();

  const [openSnack, setOpenSnack] = React.useState(false);

  const dispatch = useDispatch();

  const handleSaveNewCustomFields = useCallback(() => {
    dispatch(saveNewCustomFields(userProfile));
  }, [dispatch, userProfile]);

  useEffect(() => {
    if (snackSettings.message) {
      setOpenSnack(true);
    }
  }, [snackSettings]);

  return (
    <Card className={classes.root}>
      <CardContent>
        <Snackbar
          snackData={snackSettings}
          open={openSnack}
          setOpenSnack={setOpenSnack}
        />
        <CustomFielsList />
        <Button
          className={classes.button}
          disabled={!customFieldSaveOn}
          variant="contained"
          color="primary"
          endIcon={<PublishIcon />}
          onClick={handleSaveNewCustomFields}
        >
          {trad.save}
        </Button>
      </CardContent>
    </Card>
  );
};

export default CustomFieldEditor;
