import React, { useCallback, useState, Fragment } from "react";
import { useSelector, useDispatch } from "react-redux";
import { setEmailContent, sendEmail } from "../../../store/actions";
import Spinner from "../../navigation/Spinner";
import { Grid } from "@material-ui/core/";
import TabHeader from "../TabHeader";
import RecipientsBlock from "./RecipientsBlock";
import SendButton from "./SendButton";
import SubjectBlock from "./SubjectBlock";
import EmailContentEditor from "./EmailContentEditor";
import ConfirmationModal from "./ConfirmationModal";
import HistoryTables from "./HistoryTables";


// MAIN COMPONENT
const EmailEditorWrapper = () => {
  const {
    globalMode,
    isLoading,
    courses,
    selectedCourse,
    emailContent,
  } = useSelector((state) => ({
    selectedCourse: state.userManagement.selectedCourse,
    globalMode: state.userManagement.displayGlobalMode,
    courses: state.userManagement.courses,
    isLoading: state.userManagement.isLoading,
    emailContent: state.userManagement.emailContent,
  }));

  const dispatch = useDispatch();
  const [openModal, setOpenModal] = useState(false);

  const toggleModal = useCallback(() => {
    setOpenModal(!openModal);
  }, [openModal]);

  const handleSendEmail = useCallback(() => {
    let coursesArray = [];
    if (globalMode) {
      coursesArray = Object.keys(courses);
    } else {
      coursesArray = [selectedCourse];
    }
    dispatch(sendEmail(coursesArray, emailContent, courses, globalMode));
    toggleModal();
  }, [
    dispatch,
    toggleModal,
    courses,
    emailContent,
    globalMode,
    selectedCourse,
  ]);

  const getCohortsList = useCallback(() => {
    let cohortsList = {}
    if(globalMode) { 
      let coursesList = Object.values(courses)
      let globalCohorts = {}
      // GET ALL MICROSITE COHORTS
      coursesList.forEach(course => {
        cohortsList = {...cohortsList, ...course.cohorts}
      })
      // FILTER COHORTS WHICH ARE COMMON TO AT LEAST 2 COURSES FOR GLOBAL MODE
      Object.entries(cohortsList).forEach(cohort => {
        if(!globalCohorts.hasOwnProperty(cohort[1])){
          globalCohorts[cohort[1]] = [cohort[0]]
        } else {
          globalCohorts[cohort[1]].push(cohort[0])
        }
      })     
      Object.entries(globalCohorts).forEach(cohort => {
        if(cohort[1].length < 2) {
          delete globalCohorts[cohort[0]]
        }
      })
      return globalCohorts
    } else {
      cohortsList = courses[selectedCourse].cohorts
      let reversedCohorts = {}
      Object.entries(cohortsList).forEach(cohort => {
        reversedCohorts[cohort[1]] = cohort[0]
      })   

     return reversedCohorts
    }
  },[courses, globalMode, selectedCourse])



  if (isLoading) {
    return <Spinner />;
  }
  return (
    <Fragment>
      <TabHeader />
      <Grid container>
        <RecipientsBlock emailContent={emailContent} cohorts={getCohortsList()}/>
        <SendButton emailContent={emailContent} toggleModal={toggleModal} />
      </Grid>
      <SubjectBlock
        courseName={!globalMode && courses[selectedCourse].display_name}
        setEmailContent={setEmailContent}
        subject={emailContent.subject}
      />
      <EmailContentEditor
        setEmailContent={setEmailContent}
        message={emailContent.message}
      />
      <ConfirmationModal
        openModal={openModal}
        toggleModal={toggleModal}
        handleSendEmail={handleSendEmail}
      />
      {!globalMode && <HistoryTables dataSource={"emailsHistory"}/>}
      {!globalMode && <HistoryTables dataSource={"tasksHistory"} />}
    </Fragment>
  );
};

export default EmailEditorWrapper;
