import React from "react";
import { useSelector} from "react-redux";

import {
  makeStyles,
  Typography,
  Grid,
  Button,
  Modal,
  Backdrop,
} from "@material-ui/core/";

const useStyles = makeStyles((theme) => ({
  sendButton: {
    width: "150px",
    margin: "10px 8px",
  },
  modal: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  },
  paper: {
    backgroundColor: theme.palette.background.paper,
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
}));

const ConfirmationModal = ({ openModal, toggleModal, handleSendEmail }) => {

  const { trad } = useSelector((state) => ({
    trad: state.translations.values,
  }));

  const classes = useStyles();
  const { sendButton, paper, modal } = classes;
  return (
    <Modal
      className={modal}
      open={openModal}
      onClose={toggleModal}
      closeAfterTransition
      BackdropComponent={Backdrop}
      BackdropProps={{
        timeout: 500,
      }}
    >
      <Grid className={paper}>
        <Typography variant="h6" component="div" gutterBottom>
          {trad.confirmation}
        </Typography>
        <Typography variant="subtitle2" component="div" gutterBottom>
          {trad.send_email_validation}
        </Typography>
        <Grid item>
          <Button
            className={sendButton}
            variant="contained"
            color="primary"
            onClick={handleSendEmail}
          >
            {trad.confirm}
          </Button>
          <Button
            className={sendButton}
            variant="contained"
            color="primary"
            onClick={toggleModal}
          >
            {trad.cancel}
          </Button>
        </Grid>
      </Grid>
    </Modal>
  );
};

export default ConfirmationModal;
