import React, { Fragment } from "react";
import { Grid } from "@material-ui/core";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import CheckBoxIcon from "@material-ui/icons/CheckBox";
import CancelIcon from "@material-ui/icons/Cancel";
import moment from "moment";

const useStyles = makeStyles(() => ({
  text: {
    marginLeft: "16px",
  },
}));

const RenderRegistrationStatus = ({ startDate, endDate, trad }) => {
  const classes = useStyles();
  const today = moment().format("YYYY-MM-DDTHH:mm:ssZ");
  const enrollmentDate = moment(startDate);
  const endEnrollmentDate = moment(endDate);

  if (!startDate || enrollmentDate.isAfter(today)) {
    return (
      <Fragment>
        <Grid container alignItems="center">
          <Grid item>
            <CancelIcon />
          </Grid>
          <Grid>
            <Typography className={classes.text}>
              {trad.closed_registration}
            </Typography>
          </Grid>
        </Grid>
      </Fragment>
    );
  } else {
    if (endEnrollmentDate.isAfter(today)) {
      return (
        <Fragment>
          <Grid container alignItems="center">
            <Grid item>
              <CheckBoxIcon />
            </Grid>
            <Grid>
              <Typography className={classes.text}>
                {trad.open_registration} {trad.from}{" "}
                {moment(startDate).format("DD/MM/YYYY")} {trad.to}{" "}
                {moment(endDate).format("DD/MM/YYYY")}
              </Typography>
            </Grid>
          </Grid>
        </Fragment>
      );
    } else if (!endDate) {
      return (
        <Fragment>
          <Grid container alignItems="center">
            <Grid item>
              <CheckBoxIcon />
            </Grid>
            <Grid>
              <Typography className={classes.text}>
                {trad.open_registration} {trad.since}{" "}
                {moment(startDate).format("DD/MM/YYYY")}
              </Typography>
            </Grid>
          </Grid>
        </Fragment>
      );
    } else {
      return (
        <Fragment>
          <Grid container alignItems="center">
            <Grid item>
              <CancelIcon />
            </Grid>
            <Grid>
              <Typography className={classes.text}>
                {trad.closed_registration}
              </Typography>
            </Grid>
          </Grid>
        </Fragment>
      );
    }
  }
};

export default RenderRegistrationStatus;
