import React, { useCallback } from "react";
import { useSelector } from "react-redux";
import { useDispatch } from "react-redux";
import { TextField, makeStyles, Typography } from "@material-ui/core/";

const useStyles = makeStyles((theme) => ({
  editor: {
    padding: "8px 16px",
    fontSize: "12px",
    fontWeight: 700,
    marginBottom: 2,
    color: theme.palette.primary.main,
    textTransform: "uppercase",
  },
}));

const SubjectBlock = ({ courseName, setEmailContent, subject }) => {
  const { trad } = useSelector((state) => ({
    trad: state.translations.values,
  }));
  
  const classes = useStyles();
  const { editor } = classes;
  const dispatch = useDispatch();
  const handleEditorChange = useCallback(
    (event) => {
      const { name, value } = event.target;
      dispatch(setEmailContent(name, value));
    },
    [dispatch, setEmailContent]
  );
  return (
    <>
      <Typography variant="h6" component="div" className={editor}>
        {trad.subject}
      </Typography>
      <TextField
        style={{ margin: 8, padding: "8px 0" }}
        fullWidth
        rowsMax={1}
        name="subject"
        value={subject}
        placeholder={
          courseName ? "Ex: About your cursus" : `Ex: About ${courseName}`
        }
        onChange={handleEditorChange}
      />
    </>
  );
};

export default SubjectBlock;
