import { combineReducers } from "redux";
import userManagement from "./userManagementReducer";
import quickSearch from "./quickSearchReducer";
import translations from "./translationsReducer";
import navigation from "./navigationReducer";
import configuration from "./configurationReducer";
import openedXConverter from "./openedXConverterReducer"

export default combineReducers({
  userManagement: userManagement,
  quickSearch: quickSearch,
  translations: translations,
  navigation: navigation,
  configuration: configuration,
  openedXConverter: openedXConverter
});
