import React, { useEffect, useState, useCallback } from "react";
import { useSelector, useDispatch } from "react-redux";
import {
  Button,
  CircularProgress,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  Grid,
} from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import CachedIcon from "@material-ui/icons/Cached";
import { generateNewPasswordLink } from "../../store/actions";

const useStyles = makeStyles((theme) => ({
  button: {
    margin: theme.spacing(1),
  },
  spinner: {
    textAlign: "center",
    margin: "auto",
    marginBottom: 20,
  },
  dialogAction: {
    minHeight: "45px",
  },
}));

const IconLabelButtons = () => {
  const classes = useStyles();
  const dispatch = useDispatch();

  const [open, setOpen] = useState(false);
  const [loading, setLoading] = useState(false);

  const { org, email, passwordLink, trad } = useSelector((state) => ({
    org: state.quickSearch.userProfile.org,
    email: state.quickSearch.userProfile.email,
    passwordLink: state.quickSearch.passwordLink,
    trad: state.translations.values,
  }));

  const toggleDialog = useCallback(() => {
    setOpen(!open);
  }, [open]);

  const launchPasswordGeneration = useCallback(() => {
    setLoading(true);
    dispatch(generateNewPasswordLink(email));
  }, [dispatch, email]);

  useEffect(() => {
    passwordLink && setLoading(false);
  }, [passwordLink]);

  const renderDialogActions = useCallback(() => {
    if (loading) {
      return (
        <DialogActions className={classes.dialogAction}>
          <Grid className={classes.spinner}>
            <CircularProgress thickness={2} size={40} />
          </Grid>
        </DialogActions>
      );
    } else if (passwordLink) {
      return (
        <DialogActions className={classes.dialogAction}>
          <Button onClick={toggleDialog} color="primary" autoFocus>
            OK
          </Button>
        </DialogActions>
      );
    } else {
      return (
        <DialogActions className={classes.dialogAction}>
          <Button onClick={() => launchPasswordGeneration()} color="primary">
            {trad.generate}
          </Button>
          <Button onClick={toggleDialog} color="primary" autoFocus>
          {trad.cancel}
          </Button>
        </DialogActions>
      );
    }
  }, [
    classes.dialogAction,
    classes.spinner,
    launchPasswordGeneration,
    loading,
    passwordLink,
    toggleDialog,
    trad
  ]);
  const linkToResetPassword = "https://" + org + "/" + passwordLink
  console.log(linkToResetPassword)
  const renderDialogContent = useCallback(() => {
    if (passwordLink) {
      return (
        <DialogContent>
          <DialogContentText>
            <p>
            {trad.reset_link_password}
              <br />
              {linkToResetPassword}
            </p>
          </DialogContentText>
        </DialogContent>
      );
    }
    return (
      <DialogContent>
        <DialogContentText>
          {trad.generate_a_link}
        </DialogContentText>
      </DialogContent>
    );
  }, [linkToResetPassword, passwordLink, trad]);

  const renderDialog = useCallback(() => {
    return (
      <Dialog open={open} onClose={toggleDialog}>
        <DialogTitle>{trad.reset_password}</DialogTitle>
        {renderDialogContent()}
        {renderDialogActions()}
      </Dialog>
    );
  }, [open, renderDialogActions, renderDialogContent, toggleDialog, trad]);

  const renderResetButton = useCallback(() => {
    return (
      <Grid style={{ textAlign: "center" }}>
        <Button
          variant="contained"
          color="primary"
          className={classes.button}
          startIcon={<CachedIcon />}
          onClick={toggleDialog}
        >
          {trad.reset_password}
        </Button>
      </Grid>
    );
  }, [classes.button, toggleDialog, trad]);

  return (
    <>
      {renderResetButton()}
      {renderDialog()}
    </>
  );
};

export default IconLabelButtons;
