import React, { useEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { makeStyles } from "@material-ui/core/styles";
import {
  Grid,
  CircularProgress,
  Card,
  CardContent,
  Divider,
  Box,
} from "@material-ui/core";
import IconButton from "@material-ui/core/IconButton";
import Typography from "@material-ui/core/Typography";
import CheckCircleOutlineIcon from "@material-ui/icons/CheckCircleOutline";
import NotInterestedIcon from "@material-ui/icons/NotInterested";
import DonutLargeIcon from "@material-ui/icons/DonutLarge";
import FlagOutlinedIcon from "@material-ui/icons/FlagOutlined";
import {
  setNewNavigationValue,
  filterUsersEnrollmentsByCourse,
} from "../../store/actions";

const squareSize = "40px";
const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    padding: 20,
  },
  details: {
    display: "flex",
    flexDirection: "column",
    width: "100%",
  },
  content: {
    flex: "1 0 auto",
  },
  cover: {
    width: 151,
  },
  controls: {
    display: "flex",
    alignItems: "center",
    paddingLeft: theme.spacing(1),
    paddingBottom: theme.spacing(1),
  },
  playIcon: {
    height: 38,
    width: 38,
  },
  greenSquare: {
    backgroundColor: "#27ad60",
    height: squareSize,
    width: squareSize,
    borderRadius: "10%",
  },
  yellowSquare: {
    backgroundColor: "#f2984a",
    height: squareSize,
    width: squareSize,
    borderRadius: "10%",
  },
  redSquare: {
    backgroundColor: "#ec5756",
    height: squareSize,
    width: squareSize,
    borderRadius: "10%",
  },
  completionIcon: {
    margin: "8px",
  },
  courseList: {
    height: "300px",
    overflowY: "scroll",
  },
  courses_text: {
    width: "100%",
  },
  pointer: {
    cursor: "pointer",
  },
  // hidden:{
  //   visibility:"hidden"
  // }
}));



const CoursesProgress = () => {
  const classes = useStyles();

  const dispatch = useDispatch();

  const linkToTheCourse = (courseId) => {
    dispatch(setNewNavigationValue(1));
    dispatch(filterUsersEnrollmentsByCourse(enrollments, courseId));
  };

  const { userProfile, trad, enrollments, configuration } = useSelector((state) => ({
    userProfile: state.quickSearch.userProfile,
    trad: state.translations.values,
    enrollments: state.userManagement.enrollments,
    configuration: state.configuration.values
  }));

  const [rateProgress, setRateProgress] = useState(0);

  // const coursesList = Object.keys(userProfile.enrollments);

  // sort the course list by : in progress/passed/not enrolled

  let displayNotEnrolledCourseInList = true
  let sortedCourseList = []

  if (configuration) {
    if (configuration.HIDE_NOT_ENROLLED_COURSES_IN_QUICKSEARCH){
      displayNotEnrolledCourseInList = false
    }
  }
  
  const inProgressCourseList = Object.values(userProfile.enrollments).filter(course => 
    course.enrolled_to_enrollment && !course.passed
  )
  inProgressCourseList.map(course => 
    sortedCourseList.push(course.id)
  )

  const passedCourseList = Object.values(userProfile.enrollments).filter(course => 
     course.passed
  )
  passedCourseList.map(course => 
    sortedCourseList.push(course.id)
  )
  
  if (displayNotEnrolledCourseInList) {
    const notEnrolledCourseList = Object.values(userProfile.enrollments).filter(course => 
      !course.enrolled_to_enrollment
    )
    notEnrolledCourseList.map(course => 
      sortedCourseList.push(course.id)
    )
  }

  const getCourse = (enrollmentKey) => {
    const thisCourse = userProfile.enrollments[enrollmentKey];
    let completionIcon;
    let completionSubtitle;

    if (thisCourse.passed) {
      completionIcon = (
        <div className={classes.greenSquare}>
          <CheckCircleOutlineIcon
            htmlColor="white"
            className={classes.completionIcon}
          />
        </div>
      );
      completionSubtitle = trad.passed;
    } else if (thisCourse.start_date) {
      completionIcon = (
        <div className={classes.yellowSquare}>
          <DonutLargeIcon
            htmlColor="white"
            className={classes.completionIcon}
          />
        </div>
      );
      completionSubtitle = trad.in_progress;
    } else {
      completionIcon = (
        <div className={classes.redSquare}>
          <NotInterestedIcon
            htmlColor="white"
            className={classes.completionIcon}
          />
        </div>
      );
      completionSubtitle = trad.not_started;
    }

    return (
      <div className={classes.controls} key={enrollmentKey}>
        <Grid container justify="space-around" alignItems="center">
          <Grid item xs={1}>
            {completionIcon}
          </Grid>
          <Grid item xs={6}>
            <Grid
              container
              justify="center"
              direction="column"
              alignItems="center">
              <Grid item className={classes.courses_text}>
                <Typography
                  className={classes.pointer}
                  component="p"
                  variant="body2"
                  onClick={() => linkToTheCourse(thisCourse.id)}>
                  {thisCourse.course_name}
                </Typography>
              </Grid>
              <Grid item className={classes.courses_text}>
                <Typography
                  component="p"
                  variant="subtitle2"
                  color="textSecondary">
                  {completionSubtitle}
                </Typography>
              </Grid>
            </Grid>
          </Grid>
          <Grid item xs={2}>
            <IconButton
              disabled={!thisCourse.start_date}
              onClick={() => window.open(thisCourse.course_grades, "_blank")}>
              <Grid
                container
                justify="flex-end"
                direction="row"
                alignContent="center">
                <Grid item>
                  <Typography component="p" variant="subtitle2">
                    {trad.show_details}
                  </Typography>
                </Grid>
              </Grid>
            </IconButton>
          </Grid>
        </Grid>
      </div>
    );
  };



  const totalCoursesEnrolled = inProgressCourseList.length + passedCourseList.length

  // let rawRate = 0;
  // for (let i = 0; i < coursesList.length; i++) {
  //   if (userProfile.enrollments[coursesList[i]].passed) {
  //     rawRate += 1;
  //   }
  // }
  const rate =   passedCourseList.length / totalCoursesEnrolled * 100
  const step = rate / 20;
  const interval = 25;

  useEffect(() => {
    // const updateProgress = () => setRateProgress(rateProgress + step);
    const updateProgress = () => setRateProgress(rate);
    if (rateProgress < rate) {
      setTimeout(updateProgress, interval);
    }
  }, [rateProgress, rate, step]);

  return (
    <Card className={classes.root}>
      <div className={classes.details}>
        <CardContent className={classes.content}>
          <Grid container justify="center" alignItems="center">
            <Grid item xs={10}>
              <Grid
                container
                justify="center"
                direction="column"
                alignItems="center">
                <Grid item>
                  <Typography component="h6" variant="h6">
                    {trad.course_completed}
                  </Typography>
                </Grid>
                <Divider />
                <Grid item>
                  <Typography component="h5" variant="h5">
                    <Box fontWeight={700}>{Math.round(rateProgress)}%</Box>
                  </Typography>
                </Grid>
              </Grid>
            </Grid>
            <Grid item xs={2}>
              <div style={{ float: "right", position: "relative" }}>
                <FlagOutlinedIcon
                  fontSize="large"
                  style={{
                    color: "#2195f4",
                    position: "absolute",
                    left: "8px",
                    bottom: "11px",
                  }}
                />
                <CircularProgress
                  variant="static"
                  size={50}
                  value={Math.round(rateProgress)}
                  style={{ color: "#2195f4" }}
                />
              </div>
            </Grid>
          </Grid>
        </CardContent>
        <div className={classes.courseList}>
          {sortedCourseList.map((enrollmentKey) => {
            return getCourse(enrollmentKey);
          })}
        </div>
      </div>
    </Card>
  );
};

export default CoursesProgress;
