import React, { Fragment } from "react";
import { useSelector, useDispatch } from "react-redux";
import {
  ListItem,
  ListItemIcon,
  ListItemText,
  Divider,
  Grid,
} from "@material-ui/core";
import AccessTimeIcon from "@material-ui/icons/AccessTime";
import "date-fns";
import DateFnsUtils from "@date-io/date-fns";
import { makeStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import {
  MuiPickersUtilsProvider,
  KeyboardDatePicker,
} from "@material-ui/pickers";
import { updateTimeStamps} from "../../../store/actions";
import moment from "moment";
import frLocale from "date-fns/locale/fr";
import { setNewNavigationValue, filterUsersEnrollmentsByCourse} from "../../../store/actions";

const useStyles = makeStyles((theme) => ({
  container: {
    display: "flex",
    flexDirection: "row",
    [theme.breakpoints.down(650)]: {
      flexDirection: "column",
      margin: "auto",
    },
    width: "100%"
  },
  listText: {
    marginRight:"1em"
  },
  listTextElement:{
    maxWidth: "200px",
    cursor:"pointer",
    [theme.breakpoints.down(1275)]: {
      maxWidth: "none"
    },
    [theme.breakpoints.down(800)]: {
      maxWidth: "300px"
    },
  },
  listTextElementSub:{
    maxWidth: "200px",
    [theme.breakpoints.down(1275)]: {
      maxWidth: "none"
    },
    [theme.breakpoints.down(800)]: {
      maxWidth: "300px"
    },
    fontSize:"0.8em"
  },


}));

const TimeStampEditor = () => {
  const classes = useStyles();
  const dispatch = useDispatch();

  const { trad, userProfile, enrollments } = useSelector((state) => ({
    userProfile: state.quickSearch.userProfile,
    enrollments: state.userManagement.enrollments,
    trad: state.translations.values,
  }));

  const linkToTheCourse = (courseId) => {
    dispatch(setNewNavigationValue(1));
    dispatch(filterUsersEnrollmentsByCourse(enrollments, courseId));
  }

  const newCustomFields = {};
  const timeStampArray = Object.keys(userProfile.enrollments);
  
  timeStampArray.map((element) => {
    return (newCustomFields[element] = userProfile.custom_field[element]);
  });

  //methode to transform newCustomFields in a new object who undisplay course when value == undefined and sort the courses by date
  const sortable = [];
  for (const course in newCustomFields) {
    if (newCustomFields[course]){
      sortable.push([course, newCustomFields[course]]);
    }
  }
  sortable.sort(([,a],[,b]) => a-b)
  const objSorted = {}
  sortable.forEach(function(item){
      objSorted[item[0]]=item[1]
  })
  return (
    <Fragment>
     {Object.entries(objSorted).map(([courseId, value]) => { 
        const aMonthInMillisecond = 2629743000;
        const numberValue = (parseInt(value, 10))
        const endOfAccesDate = numberValue + aMonthInMillisecond

        // const date = moment(numberValue).format("YYYY-MM-DD");
        const date = moment(endOfAccesDate).format("YYYY-MM-DD");

        const handleDateChange = (event) => {
          const unixDatePlus30days = moment(event).unix() * 1000;
          // const unixDate = (moment(event).unix() - aMonthInMillisecond) * 1000;
          const unixDate = unixDatePlus30days - aMonthInMillisecond
          dispatch(updateTimeStamps(unixDate, courseId));
        };

        return (
          <Fragment key={courseId}>
            <ListItem>
              <ListItemIcon>
                <AccessTimeIcon />
              </ListItemIcon>
              <Grid className={classes.container}>
                <ListItemText
                  className={classes.listText}
                  disableTypography
                  primary={<Typography className={classes.listTextElement} onClick={() => linkToTheCourse(userProfile.enrollments[courseId].id)} >{userProfile.enrollments[courseId].course_name} </Typography> } 
                  secondary={<Typography className={classes.listTextElementSub} >{courseId}</Typography>}
                />
                <MuiPickersUtilsProvider utils={DateFnsUtils} locale={frLocale}>
                  <KeyboardDatePicker
                    margin="normal"
                    label={trad.register_date}
                    format="dd/MM/yyyy"
                    value={date}
                    helperText={''}
                    onChange={handleDateChange}
                    KeyboardButtonProps={{
                      "aria-label": "change date",
                    }}
                  />
                </MuiPickersUtilsProvider>
              </Grid>
            </ListItem>
            <Divider />
          </Fragment>
        );
      })}
    </Fragment>
  );
};

export default TimeStampEditor;

