import {
    SET_NEW_NAVIGATION_VALUE
  } from "../actions/types";
  

const INITIAL_STATE = {
    navValue:1,
  };
  
  const navigationReducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case SET_NEW_NAVIGATION_VALUE:
            return {
              ...state,
                navValue: action.payload,
            };
      default:
        return state;
    }
  };
  
  export default navigationReducer;